import showScene from '../../common/showScene';
import { RES_COMMON } from '../../resource/common';
import HeaderLayer from '../base/HeaderLayer';
import { goBack } from '../../common/invoke';
import VoiceManager from '../../common/voiceManager';

const IntroLayer = cc.Layer.extend({
    ctor({
        bgLayer = new cc.LayerColor(cc.color(255, 255, 255)),
        intro = null,
        btn = null,
        name = '',
        mute = null,
        gogo = null,
        unmute = null,
        quit = RES_COMMON.btn_quit,
        voice = null,
        dx = 0, // 图片偏移中心距离x方向
        dy = 0,
    }, cbNext, examKey) {
        this._super();

        const header = new HeaderLayer(examKey, goBack, false, false, gogo, {
            unmuteImage: unmute,
            muteImage: mute,
            backImage: quit,
        });
        this.addChild(header, 1);
        this.initBackground(bgLayer);
        this.initIntro(name, intro, dx, dy);
        this.initMenu(btn, cbNext);
        if (voice) {
            VoiceManager.playVoice(voice);
        }
    },

    initBackground(background) {
        if (background instanceof cc.Layer) {
            background.setPosition(0, 0);
            this.addChild(background);
        }
    },

    initIntro(name, intro, dx, dy) {
        const { width, height } = cc.director.getWinSize();
        if (intro !== name) {
            const img = new cc.Sprite(intro);
            img.setAnchorPoint(0.5, 0.5);
            img.setPosition((width / 2) + dx, (height / 2) + 72 + dy);
            this.addChild(img);
        } else {
            const lblTitle = new cc.LabelTTF(`${name}`, '', 40);
            lblTitle.setColor(cc.color.BLACK);
            lblTitle.setPosition(width / 2, height / 2);
            this.addChild(lblTitle);
        }
    },

    initMenu(btn, cbNext) {
        const menu = new cc.Menu();
        menu.setPosition(0, 0);
        this.addChild(menu);

        const { width } = cc.director.getWinSize();
        const btnBegin = new cc.MenuItemImage(btn, null, cbNext);
        btnBegin.setPosition(width / 2, 100);
        menu.addChild(btnBegin);
    },
});

export default function showIntroScene(config = {}, cbNext, examKey) {
    showScene(new IntroLayer(config, cbNext, examKey));
}



