import showAppleExamScene from './AppleExamScene';
import showScene from '../../common/showScene';
import VoiceManager from '../../common/voiceManager';
import { RES_APPLE } from '../../resource/apple';
import { generatePlatePosition, isSamePosition } from './position';
import { generatePosition } from './round';
import { EXAM_KEY_APPLE } from '../../constant/exam';
import GuideOverLayer from '../../common/GuideOverLayer';
import HeaderLayer from '../base/HeaderLayer';
import BGLayer from './BGLayer';
import HandleUtil from '../base/HandleUtil';
import ErrorTips from '../base/ErrorTips';
import GuideFirstPage from '../base/GuideFirstPage';

const TAG_RED_POINT = 2;
const TAG_APPLE = 3;
const TAG_SELECTED = 5;

const plateCount = 2;

const AppleGuideLayer = cc.Layer.extend({
    _redPointIndex: 0,
    _redPointPosition: null,
    _applePositionIndex: 0,
    _applePosition: null,
    _enablePress: null,
    _config: null,
    _hand: null,
    _error: null,


    ctor() {
        this._super();
        this._enablePress = false;
        this._config = generatePosition();
        const {
            redPointIndex, redPointPosition, applePositionIndex, applePosition,
        } = this._config[0];

        this._redPointIndex = redPointIndex;
        this._redPointPosition = redPointPosition;
        this._applePositionIndex = applePositionIndex;
        this._applePosition = applePosition;

        this.platePositions = generatePlatePosition(plateCount);


        const bgLayer = new BGLayer();
        this.addChild(bgLayer);

        const header = new HeaderLayer(
            EXAM_KEY_APPLE,
            this.onSkip.bind(this),
            null,
            null,
            this.onGuideAgain.bind(this),
        );
        this.addChild(header, 99);
        this.addChild(new GuideFirstPage(EXAM_KEY_APPLE, 5, new BGLayer()), 88);
        const menu = new cc.Menu();
        menu.setPosition(0, 0);
        this._hand = new HandleUtil(menu, false);
        this._error = new ErrorTips(menu, false);

        for (let i = 0; i < this.platePositions.length; i += 1) {
            const plateMenuItem = new cc.MenuItemImage(RES_APPLE.plate, null, this.onPlatePressed, this);
            plateMenuItem.setPosition(this.platePositions[i]);
            menu.addChild(plateMenuItem, 0, i);
        }

        this.addChild(menu);
    },

    onEnter() {
        this._super();

        this.playGuideVoice();
    },

    playGuideVoice() {
        VoiceManager.playVoice(RES_APPLE.guide, this.redPointAppear.bind(this));
    },

    redPointAppear() {
        const redPoint = new cc.Sprite(RES_APPLE.red_point);
        redPoint.setPosition(this._redPointPosition);
        this.addChild(redPoint, 1, TAG_RED_POINT);
        VoiceManager.playVoice(RES_APPLE.red_point_appear, this.redPointDisappear.bind(this));
    },

    redPointDisappear() {
        this.removeChildByTag(TAG_RED_POINT);
        this.scheduleOnce(this.appleAppear, 1);
    },

    appleAppear() {
        this._hand.show(cc.p(this._applePosition.x + 90, this._applePosition.y - 133));
        const apple = new cc.Sprite(RES_APPLE.apple);
        apple.setPosition(this._applePosition);
        this.addChild(apple, 1, TAG_APPLE);
        VoiceManager.playVoice(RES_APPLE.apple_appear);
        this._enablePress = true;
    },

    onPlatePressed(menuItem) {
        if (!this._enablePress) {
            return;
        }
        this._enablePress = false;

        const position = menuItem.getPosition();
        const isCorrect = isSamePosition(position, this._applePosition);

        if (isCorrect) {
            this._hand.hide();
            this.selectedAppear(position);
            this._error.hide();
            VoiceManager.playVoice(RES_APPLE.correct);
            this.guideOver();
        } else {
            this._error.hide();
            this._error.show(cc.p(menuItem.x + 110, menuItem.y - 110));
            VoiceManager.playVoice(RES_APPLE.incorrect, this.selectedDisappear.bind(this));
        }
    },

    selectedAppear(position) {
        const selector = new cc.Sprite(RES_APPLE.selected);
        selector.setPosition(position);
        this.addChild(selector, 1, TAG_SELECTED);
    },

    selectedDisappear() {
        this._error.hide();
        if (this.getChildByTag(TAG_SELECTED)) {
            this.removeChildByTag(TAG_SELECTED);
        }
        this._enablePress = true;
    },

    onGuideAgain() {
        showAppleGuideScene();
    },

    guideOver() {
        const overLayer = new GuideOverLayer(
            this.onGuideAgain.bind(this),
            this.onSkip.bind(this),
            EXAM_KEY_APPLE,
        );
        this.addChild(overLayer, 999);
    },

    onSkip() {
        VoiceManager.stopVoice();
        showAppleExamScene();
    },
});

export default function showAppleGuideScene() {
    showScene(new AppleGuideLayer());
}



