class ConfigManager {
    constructor() {
        this.babyDays = null;
        this.baby = {};
        this.scoreParams = {};
        setTimeout(this.init.bind(this), 0);
    }
    init() {
        if (window.iBrainConfig) {
            const { babyDays, scoreParams, baby } = window.iBrainConfig;
            this.babyDays = babyDays;
            this.baby = typeof baby === 'string' ? JSON.parse(baby) : baby;
            this.scoreParams = typeof scoreParams === 'string' ? JSON.parse(scoreParams) : scoreParams;
        } else {
            setTimeout(this.init.bind(this), 50);
        }
    }
    getBabyAge() {
        return this.babyDays;
    }

    getBabyId() {
        return this.baby.id;
    }

    getBabyName() {
        return this.baby.name;
    }

    getLevelConfig(key, levelIndex) {
        return this.scoreParams.configs[key][levelIndex];
    }
}

export default new ConfigManager();



