import { RES_GUIDE_WAIT } from '../../resource/guideWait';
import { RES_LOLLIPOP } from '../../resource/lollipop';

const GuideFirstPage = cc.Layer.extend({

    ctor(gameName, time, bgLayer, isLollipopTip) {
        this._super();
        const winSize = cc.director.getWinSize();
        let guideSussOver = null;
        if (isLollipopTip) {
            guideSussOver = new cc.Sprite(RES_LOLLIPOP.lollipopTip);
        } else {
            guideSussOver = new cc.Sprite(RES_GUIDE_WAIT[gameName]);
        }
        guideSussOver.setPosition(winSize.width / 2, winSize.height / 2);
        guideSussOver.setAnchorPoint(0.5, 0.5);

        bgLayer.addChild(guideSussOver);
        this.addChild(bgLayer, 3, 199);

        this.scheduleOnce(this.hideNumber, time);
    },
    hideNumber() {
        if (this.getChildByTag(199)) {
            this.removeChildByTag(199);
        }
    },
});


export default GuideFirstPage;



