

import { RES_COMMON } from '../../resource/common';

const TAG_ERROR = 456;
const ErrorTips = cc.Layer.extend({
    _isShow: false,
    _menu: null,
    _tip: null,
    ctor(menu) {
        this._super();
        this._isShow = false;
        this._menu = menu;
        this._tip = new cc.MenuItemImage(
            RES_COMMON.error_tips,
            null,
            null,
            this,
        );
    },

    show(position) {
        if (this._isShow) return;
        this._isShow = true;
        if (position) {
            this._tip.setPosition(position);
        }
        this._menu.addChild(this._tip, 0, TAG_ERROR);
    },
    hide() {
        if (!this._isShow) return;
        this._isShow = false;
        this._menu.removeChildByTag(TAG_ERROR);
    },
});

export default ErrorTips;



