const twoPlatesPosition = [
    cc.p(386, 368),
    cc.p(926, 368),
];

const fourPlatesPosition = [
    cc.p(316, 364),
    cc.p(1024, 364),
    cc.p(666, 540),
    cc.p(666, 210),
];

export function generatePlatePosition(plateCount) {
    return plateCount === 2 ? twoPlatesPosition : fourPlatesPosition;
}

export function isSamePosition(positionOne, positionTwo) {
    return positionOne.x === positionTwo.x && positionOne.y === positionTwo.y;
}



