import generateExamList from './exams';
import scoreManager from '../score';
import configManager from '../config';
import showMainScene from './main';
import { setBgColor } from '../common/invoke';
import { EXAM_BG_COLORS } from '../constant/common';

const getHtmlParams = (search) => {
    const params = {};
    if (search && search.length > 1) {
        const searchString = search.substring(1);
        const items = searchString.split('&');
        for (let i = 0; i < items.length; i += 1) {
            if (items[i]) {
                const keyValue = items[i].split('=');
                params[keyValue[0]] =
          typeof keyValue[1] === 'undefined' ? '' : keyValue[1];
            }
        }
    }

    return params;
};

class ExamManager {
    constructor() {
        this._examRunners = [];
        this._result = {};
        this._begin = null;
        this._currentExamIndex = 0;

        this._isFromApp = false;
        this._startIndex = 0;
        this._type = '';
    }

    start() {
        const type = getHtmlParams(window.location.search);
        if (type && type.type) {
            this._isFromApp = true;
            this._type = type.type;
            switch (this._type) {
                case 'attention':
                    this._currentExamIndex = 0;
                    this._startIndex = 0;
                    break;
                case 'memory':
                    this._currentExamIndex = 2;
                    this._startIndex = 2;
                    break;
                case 'solution':
                    this._currentExamIndex = 4;
                    this._startIndex = 4;
                    break;
                case 'executive':
                    this._currentExamIndex = 6;
                    this._startIndex = 6;
                    break;
                default:
                    this._currentExamIndex = 0;
                    this._startIndex = 0;
                    break;
            }
        }

        this._begin = new Date();

        this._examRunners = generateExamList(configManager.getBabyAge());

        showMainScene();
    }

    hasNext() {
        if (this._isFromApp) {
            return this._currentExamIndex < this._startIndex + 2;
        }
        return this._currentExamIndex <= this._examRunners.length - 1;
    }

    next() {
        if (this.hasNext()) {
            this._examRunners[this._currentExamIndex]();
            setBgColor(EXAM_BG_COLORS[this._currentExamIndex]);
        } else {
            scoreManager.showScore(this._result, this._begin, new Date(), this._type);
        }

        this._currentExamIndex += 1;
    }

    stop() {
    // TODO: 停止评估，通知app隐藏当前页
    }

    reportResult(key, result) {
        this._result[key] = result;
    }
}

export default new ExamManager();



