import showScene from '../../common/showScene';
import showCompleteScene from '../base/CompleteScene';
import { EXAM_KEY_LOLLIPOP } from '../../constant/exam';
import { RoundConfig, RoundDelay, AllDoneDelay } from './round';
import LollipopLayer from './LollipopLayer';
import BGLayer from './BGLayer';

const TAG_LOLLIPOP_LAYER = 1;

const LollipopExamLayer = cc.Layer.extend({
    _round: 0,
    _examBegin: null,
    _roundInfos: null,

    ctor() {
        this._super();

        this._roundInfos = [];
        this._examBegin = new Date();
        this.addChild(new BGLayer());
        this.nextRound();
    },

    nextRound() {
        if (this.getChildByTag(TAG_LOLLIPOP_LAYER)) {
            this.removeChildByTag(TAG_LOLLIPOP_LAYER);
        }

        this.addChild(new LollipopLayer(RoundConfig[this._round], this.onRoundOver.bind(this)), 0, TAG_LOLLIPOP_LAYER);
    },

    onRoundOver(roundInfo) {
        this._roundInfos.push(roundInfo);

        this._round += 1;

        if (this._round < RoundConfig.length) {
            this.scheduleOnce(this.nextRound, RoundDelay);
        } else {
            this.scheduleOnce(this.allDone, AllDoneDelay);
        }
    },

    allDone() {
        showCompleteScene(
            EXAM_KEY_LOLLIPOP,
            { rounds: this._roundInfos, begin: this._examBegin, end: new Date() },
        );
    },
});

export default function showLollipopExamScene() {
    showScene(new LollipopExamLayer());
}



