
import RoundRectLayout from './RoundRectLayout';
import { TYPE_MAP, TIP_MAP } from './const';
import { RES_SCORE } from '../resource/score';

const ScoreNextLayer = cc.Layer.extend({

    ctor(height, data, types) {
        this._super();
        this._data = data;
        this.setPositionY(height);
        this.setContentSize(cc.winSize);
        this._renderPart(0, 20, types[0]);
        this._renderPart(cc.winSize.width / 2, (cc.winSize.width / 2) - 20 - 590, types[1]);
    },

    _renderPart(x, xx, type) {
        const part = new cc.Layer();
        part.setPosition(cc.p(x, 0));
        const body = this._getBody(xx, type);
        part.addChild(body);
        const head = this._getHead(xx, type);
        part.addChild(head);

        this.addChild(part);
    },

    _getHead(xx, type) {
        const rect = new RoundRectLayout(590, 106, 53);
        rect.setPosition(cc.p(xx, cc.winSize.height - 200));
        rect.setColor(cc.color(190, 245, 241));
        const text = new cc.LabelTTF(`${TYPE_MAP[type]}           ${Math.round(this._data[type].score)}分`, '', 36);
        text.setColor(cc.color(42, 42, 42));
        text.setPosition(cc.p(110, 52));
        text.setAnchorPoint(0, 0.5);
        rect.addChild(text);
        const menu = new cc.Menu();
        menu.setPosition(cc.p(530, 52));
        const sprite = new cc.Sprite(RES_SCORE.btn_display);
        sprite.setPosition(cc.p(25, 25));
        const menuItem = new cc.MenuItemSprite(sprite);
        menuItem.setContentSize(cc.size(100, 100));
        menu.addChild(menuItem);
        rect.addChild(menu);
        const display = this._getDisplay(type, menuItem);
        display.setColor(cc.color(255, 226, 122));
        display.setPositionX(10000);
        display.setOpacity(244);
        rect.addChild(display, 0, type);
        menuItem.setCallback(this._setDisplay.bind(this, display, menuItem));

        return rect;
    },

    _removeDisplay(display, menuItem) {
        display.setPositionX(10000);
        menuItem.setOpacity(255);
        menuItem.setCallback(this._setDisplay.bind(this, display, menuItem));
    },

    _setDisplay(display, menuItem) {
        display.setPositionX(0);
        menuItem.setOpacity(180);
        menuItem.setCallback(this._removeDisplay.bind(this, display, menuItem));
    },

    _getDisplay(type, mItem) {
        const tip = new cc.LabelTTF(TIP_MAP[type], '', 32);
        tip.setColor(cc.color(82, 83, 85));
        tip.setLineHeight(50);
        tip.setDimensions(cc.size(500, 0));
        tip.setAnchorPoint(0, 0);
        tip.setPosition(50, 50);

        const display = new RoundRectLayout(590, tip.height + 100, 16);
        display.setColor(cc.color(255, 226, 122));
        display.addChild(tip);
        display.setAnchorPoint(0, 0);
        display.setPositionY(-tip.height - 100);
        const menu = new cc.Menu();
        menu.setPosition(cc.p(560, tip.height + 72));
        const sprite = new cc.Sprite(RES_SCORE.btn_del);
        sprite.setPosition(cc.p(44, 44));
        const menuItem = new cc.MenuItemSprite(sprite);
        menuItem.setContentSize(cc.size(160, 160));
        menuItem.setCallback(this._removeDisplay.bind(this, display, mItem));
        menuItem.setAnchorPoint(0.5, 0.5);
        menu.addChild(menuItem);
        display.addChild(menu);
        return display;
    },

    _getBody(xx, type) {
        const assessment = new cc.LabelTTF(this._data[type].assess, '', 32);
        assessment.setColor(cc.color(82, 83, 85));
        assessment.setLineHeight(50);
        assessment.setDimensions(570, 0);
        assessment.setAnchorPoint(0.5, 1);
        assessment.setPosition(cc.p(xx + 306, cc.winSize.height - 276));
        return assessment;
    },

});

export default ScoreNextLayer;



