import { RES_CANDY } from '../../resource/candy';

const BGLayer = cc.Layer.extend({
    ctor() {
        this._super();

        this.addChild(new cc.LayerColor(cc.color(51, 49, 100)));

        const winSize = cc.director.getWinSize();
        const starSprite = new cc.Sprite(RES_CANDY.bg_star);
        starSprite.setAnchorPoint(cc.p(0.5, 1));
        starSprite.setPosition(cc.p(winSize.width / 2, winSize.height - 100));
        this.addChild(starSprite);
    },
});

export default BGLayer;



