import { TYPE_LIST, TYPE_MAP } from './const';
import RoundRectLayout from './RoundRectLayout';
import configManager from '../config';

const ALIGN_LIST = [
    ccui.RELATIVE_ALIGN_PARENT_TOP_LEFT,
    ccui.RELATIVE_ALIGN_PARENT_TOP_RIGHT,
    ccui.RELATIVE_ALIGN_PARENT_LEFT_BOTTOM,
    ccui.RELATIVE_ALIGN_PARENT_RIGHT_BOTTOM,
    ccui.RELATIVE_ALIGN_PARENT_CENTER,
];

const ScoreIndexLayer = ccui.VBox.extend({

    ctor(height, data) {
        this._super();
        this._data = data;
        const percent = this._data.total.over >= 1 ? 99.9 : (Math.round(this._data.total.over * 1000) / 10);
        this.setPositionY(height);
        this.setSize(cc.winSize);
        const name = configManager.getBabyName();
        const title = this._data.total.over === undefined
            ? `恭喜，${name} 完成了本次评估`
            : `恭喜，${name} 在本次评估中超过了 ${percent}% 的同龄孩子`;
        this._renderTitle(title);
        this._renderTable();
        this._renderAssessment(this._data.total.assess);
    },

    _renderTitle(text = '') {
        const title = new ccui.Text(text, '', 44);
        title.setColor(cc.color(0, 0, 0));
        const parameter = new ccui.LinearLayoutParameter();
        parameter.setMargin(new ccui.Margin(0, 52, 0, 52));
        parameter.setGravity(ccui.LINEAR_GRAVITY_CENTER_HORIZONTAL);
        title.setLayoutParameter(parameter);
        this.addChild(title);
    },

    _renderTable() {
        const table = new ccui.Layout();
        table.setSize(cc.size(1266, 240));
        table.setLayoutType(ccui.Layout.RELATIVE);

        this.addChild(table);
        const parameter = new ccui.LinearLayoutParameter();
        parameter.setMargin(new ccui.Margin(0, 20, 0, 72));
        parameter.setGravity(ccui.LINEAR_GRAVITY_CENTER_HORIZONTAL);
        table.setLayoutParameter(parameter);
        TYPE_LIST.forEach((type, index) => {
            const roundRect = new RoundRectLayout(580, 106, 53);
            roundRect.setColor(cc.color(190, 245, 241));

            const typeText = new ccui.Text(TYPE_MAP[type], '', 39);
            typeText.setColor(cc.color(42, 42, 42));
            typeText.setPosition(cc.p(156, 52));
            const scoreText = new ccui.Text(`${Math.round(this._data[type].score)}分`, '', 36);
            scoreText.setColor(cc.color(42, 42, 42));
            scoreText.setPosition(cc.p(400, 52));
            const param = new ccui.RelativeLayoutParameter();
            param.setAlign(ALIGN_LIST[index]);
            roundRect.setLayoutParameter(param);
            roundRect.addChild(typeText);
            roundRect.addChild(scoreText);
            table.addChild(roundRect);
        });
        const center = new RoundRectLayout(248, 248, 124);
        center.setColor(cc.color(0, 175, 165));
        const centerpParam = new ccui.RelativeLayoutParameter();
        centerpParam.setAlign(ALIGN_LIST[4]);
        center.setLayoutParameter(centerpParam);
        const number = new ccui.Text(Math.round(this._data.total.score), '', 72);
        const fen = new ccui.Text('分', '', 32);
        fen.setPosition(cc.p((center.width / 2) + 66, (center.height / 2) - 12));
        number.setPosition(cc.p((center.width / 2) - 20, (center.height / 2) + 6));
        center.addChild(fen);
        center.addChild(number);
        table.addChild(center);
    },

    _renderAssessment(text) {
        const assessmentLayout = new ccui.Layout();
        const assessment = new cc.LabelTTF(text, '', 36);
        assessment.setColor(cc.color(82, 83, 85));
        assessment.setLineHeight(56);
        assessment.setDimensions(cc.size(cc.winSize.width - 300, 0));
        assessmentLayout.addChild(assessment);
        const parameter = new ccui.LinearLayoutParameter();
        parameter.setMargin(new ccui.Margin(0, 92, 0, 72));
        parameter.setGravity(ccui.LINEAR_GRAVITY_CENTER_HORIZONTAL);
        assessmentLayout.setLayoutParameter(parameter);
        this.addChild(assessmentLayout);
    },
});

export default ScoreIndexLayer;



