export default function updateResult(result, data, funCalc, key) {
    const rs = result;

    if (data[key]) {
        const res = funCalc(data);
        rs.totalTime += (res.end.getTime() - res.begin.getTime());
        rs.result.score += res.score;
        rs.result[key] = res;
    }
}



