import calcLollipop from '../exam/lollipop';
import calcNumber from '../exam/number';
import { EXAM_KEY_LOLLIPOP, EXAM_KEY_NUMBER } from '../../../constant/exam';
import updateResult from './updateResult';

export default function calcMemory(result) {
    const memoryResult = {
        totalTime: 0,
        result: { score: 0 },
    };

    updateResult(memoryResult, result, calcLollipop, EXAM_KEY_LOLLIPOP);
    updateResult(memoryResult, result, calcNumber, EXAM_KEY_NUMBER);

    return memoryResult;
}



