const EXAM_API = {
    GO_BACK: 'goBack',
    LOAD_LIST: 'loadList',
    LOAD_DETAIL: 'loadDetail',
    SUBMIT_RESULT: 'submitResult',
    SET_MUTE: 'setMute',
    CANCEL_MUTE: 'cancelMute',
    SET_BG_COLOR: 'setBgColor',
    GET_MUTE: 'getMute',
};

const invoke = window.WebViewInvoke;
const goBack = invoke.bind(EXAM_API.GO_BACK);
const loadList = invoke.bind(EXAM_API.LOAD_LIST);
const loadDetail = invoke.bind(EXAM_API.LOAD_DETAIL);
const submitResult = invoke.bind(EXAM_API.SUBMIT_RESULT);
const setMute = invoke.bind(EXAM_API.SET_MUTE);
const cancelMute = invoke.bind(EXAM_API.CANCEL_MUTE);
const setBgColor = invoke.bind(EXAM_API.SET_BG_COLOR);
const getMute = invoke.bind(EXAM_API.GET_MUTE);
export {
    invoke,
    goBack,
    loadList,
    loadDetail,
    submitResult,
    setMute,
    cancelMute,
    setBgColor,
    getMute,
};



