const TYPE_LIST = ['attention', 'memory', 'solution', 'executive'];
const TYPE_MAP = {
    attention: '注意力',
    memory: '记忆力',
    solution: '推理力',
    executive: '执行力',
};

const LEVEL_MAP = {
    attention: [60, 120],
    memory: [60, 140],
    solution: [60, 140],
    executive: [60, 120],
    total: [240, 520],
};

const TIP_MAP = {
    attention: '重点测查警觉、注意力的转移能力。警觉，是指能够及时察觉环境的变化，注意力的转移，是指从一个需要注意的事物，转移到另外一个需要注意的事物上。',
    memory: '重点测查工作记忆能力。工作记忆是认知能力的最核心的指标之一，是指在执行认知任务中, 对信息暂时储存与操作的能力。',
    solution: '重点测查归纳推理和问题解决能力。是指通过提供线索找出匹配的目标物，和根据提供的范例进行扩展的推论过程。',
    executive: '重点测查认知灵活性和抑制控制能力。执行功能是综合认知能力的体现，是儿童发展阶段重要的标志。',
};

const ASSESS_MAP_LIST = {
    attention: [
        '宝宝的注意力得分较低，表现为难以及时察觉环境变化，以及难以将注意力从一个目标事物，转移到另外一个目标事物上。建议您和宝宝，一起多做做注意力训练的小活动 。',
        '宝宝的注意力得分中等，表现为能够及时察觉环境的变化，能够将注意力从一个目标事物，转移到另外一个目标事物上。建议您和宝宝，一起多做做注意力训练的小活动 。',
        '宝宝的注意力得分较高，表现为能够很快及时察觉环境的变化，以及能够很快将注意力从一个目标事物，转移到另外一个目标事物上。您继续和宝宝一起加油，多做注意力训练的小活动吧。',
    ],
    memory: [
        '宝宝的记忆力得分较低，表现为在执行认知任务中, 对信息的暂时储存与操作的能力较弱。建议您多和宝宝一起做做记忆训练的小活动。',
        '宝宝的记忆力得分中等，表现为在执行认知任务中, 对信息的暂时储存与操作的能力良好。建议您多和宝宝一起做做记忆训练的小活动。',
        '宝宝的记忆力得分较高，表现为在执行认知任务中, 对信息的暂时储存与操作的能力很优秀。您继续和宝宝一起加油，多做记忆训练的小活动吧。',
    ],
    solution: [
        '宝宝在推理方面的得分较低，表现为归纳推理和问题解决能力，即通过提供线索找出匹配的目标物、根据提供的范例进行扩展的推论过程的能力较弱。建议您多和宝宝一起做做推理训练的小活动。',
        '宝宝在推理方面的得分中等，表现为归纳推理和问题解决能力，即通过提供线索找出匹配的目标物、根据提供的范例进行扩展的推论过程的能力良好。建议您多和宝宝一起做做推理训练的小活动。',
        '宝宝在推理方面的得分较高，表现为归纳推理和问题解决能力，即通过提供线索找出匹配的目标物、根据提供的范例进行扩展的推论过程的能力很优秀。您继续和宝宝一起加油，多做推理训练的小活动吧。',
    ],
    executive: [
        '宝宝在执行方面的得分较低，表现为认知灵活性较弱，即不能很快地适应新情境要求，以及抑制对无关刺激的反应能力较差。建议您多和宝宝一起做做执行力主题的训练的小活动。',
        '宝宝在执行方面的得分中等，表现为认知灵活性良好，即适应新情境要求的能力一般，以及抑制对无关刺激的反应能力良好。建议您多和宝宝一起做做执行力主题的训练的小活动。',
        '宝宝在执行方面的得分较高，表现为认知灵活性较好，不固执、 不拘泥于形式、能很快地适应新情境要求，以及抑制对无关刺激的反应能力很优秀。您继续和宝宝一起加油，多做执行功能力主题的小活动吧。',
    ],
    total: [
        '宝宝在整体认知能力得分较低，',
        '宝宝在整体认知能力得分中等，',
        '宝宝在整体认知能力得分较高，',
    ],
};
// 在注意力、记忆力、推理力和执行力四个方面的能力很优秀
const numMap = ['零个方面', '', '两个方面', '三个方面', '四个方面'];

const getTextHead = (types) => {
    const { length } = types;
    return `${length === 1 ? '' : '在'}${types.join('、').replace(/、([^、]+?$)/g, '和$1')}${numMap[length]}`;
};

const getAssess = (examData) => {
    const result = {};
    const count = {
        1: [],
        2: [],
        3: [],
    };
    TYPE_LIST.forEach((type) => {
        result[type] = {};
        const level = examData[`${type}_level`];
        const assessList = ASSESS_MAP_LIST[type];
        result[type].level = level;
        result[type].assess = assessList[level - 1];
        result[type].score = examData[`${type}_score`];
        result[type].over = examData[`${type}_over_percent`];
        count[level].push(TYPE_MAP[type]);
    });
    result.total = {};
    result.total.level = examData.total_level;
    result.total.score = examData.total_score;
    result.total.assess = ASSESS_MAP_LIST.total[examData.total_level - 1];
    result.total.over = examData.total_over_percent;
    if (count['3'].length > 0) {
        result.total.assess += `${getTextHead(count['3'])}的能力很优秀，`;
    }
    if (count['2'].length > 0) {
        result.total.assess += `${getTextHead(count['2'])}的能力良好，`;
    }
    if (count['1'].length > 0) {
        result.total.assess += `${getTextHead(count['1'])}的能力一般，`;
    }
    if (count['3'].length < 4) {
        result.total.assess += '建议您多和宝宝一起做做对应主题的训练活动。';
    } else {
        result.total.assess += '您继续和宝宝一起加油哦。';
    }
    return result;
};

const getScore = (data) => {
    const result = {};
    let totalScore = 0;
    TYPE_LIST.forEach((type) => {
        const { score } = data[type];
        const level = LEVEL_MAP[type];
        totalScore += score;
        result[`${type}_score`] = score;
        switch (true) {
            case score < level[0]: result[`${type}_level`] = '1'; break;
            case score >= level[0] && score < level[1]: result[`${type}_level`] = '2'; break;
            default: result[`${type}_level`] = '3'; break;
        }
    });
    switch (true) {
        case totalScore < LEVEL_MAP.total[0]: result.total_level = '1'; break;
        case totalScore >= LEVEL_MAP.total[0] && totalScore < LEVEL_MAP.total[1]:
            result.total_level = '2'; break;
        default: result.total_level = '3'; break;
    }
    result.total_score = totalScore;
    return result;
};


export {
    TYPE_LIST,
    TYPE_MAP,
    TIP_MAP,
    getAssess,
    getScore,
};



