import { RES_SHAPE, RES_SMALL_SHAPE, RES_DISTURBER, RES_OPTIONS, RES_SELECTED } from '../../resource/shape';
import { RoundTimeout } from './round';
import { BIG_ROPE, OPTIONS_CENTRE } from './position';
import TimeoutLayer from '../../common/TimeoutLayer';
import { EXAM_KEY_SHAPE } from '../../constant/exam';


const ShapeLayer = cc.Layer.extend({
    _cbSelect: null,
    _begin: null,
    _react_begin: null,
    _roundOver: false,

    ctor({
        bigShape,
        bigShapePositions,
        smallShape,
        ropeType,
        ropePosition,
        disturberPosition,
        cbSelect,
    }) {
        this._super();

        this.isBigRope = ropeType === BIG_ROPE;
        this.bigShapePositions = bigShapePositions;
        this.smallShape = smallShape;
        this.ropePosition = ropePosition;
        this.correctShape = this.isBigRope ? bigShape : smallShape;
        this.disturberPosition = disturberPosition;
        this._cbSelect = cbSelect;
        this._roundOver = false;
    },

    onEnter() {
        this._super();
        this._begin = new Date();
        this.shapeAppear();
        this.scheduleOnce(this.onSelectTimeout, RoundTimeout);
    },

    shapeAppear() {
        const {
            bigShapePositions, disturberPosition, smallShape, isBigRope, ropePosition,
        } = this;

        for (let i = 0; i < bigShapePositions.length; i += 1) {
            const smallShapeImage = disturberPosition === i ? RES_DISTURBER[smallShape] : RES_SMALL_SHAPE[smallShape];
            const smallShapeItem = new cc.Sprite(smallShapeImage);
            smallShapeItem.setPosition(bigShapePositions[i]);
            this.addChild(smallShapeItem, 1, i);
        }

        const ropeImage = isBigRope ? RES_SHAPE.big_rope : RES_SHAPE.small_rope;
        const rope = new cc.Sprite(ropeImage);
        rope.setPosition(ropePosition);

        this.addChild(rope, 2);

        const menu = new cc.Menu();
        menu.setPosition(0, 0);

        for (let i = 0; i < OPTIONS_CENTRE.length; i += 1) {
            const menuItem = new cc.MenuItemImage(RES_OPTIONS[i], null, this.onOptionPressed, this);
            menuItem.setPosition(OPTIONS_CENTRE[i]);
            menu.addChild(menuItem, 1, i);
        }

        this.addChild(menu);

        this._react_begin = new Date();
    },

    onOptionPressed(menuItem) {
        if (this._roundOver) {
            return;
        }

        if (this._react_begin) {
            const selectedShape = menuItem.getTag();
            this.selectedAppear(selectedShape);
            const result = {
                success: this.correctShape === selectedShape,
                reaction: new Date().getTime() - this._react_begin.getTime(),
                begin: this._begin.getTime(),
                end: new Date().getTime(),
                timeout: false,
            };

            if (this._cbSelect) {
                this._roundOver = true;
                this._cbSelect(result);
            }
        }
    },

    selectedAppear(selectedShape) {
        const selectedOption = new cc.Sprite(RES_SELECTED[selectedShape]);
        selectedOption.setPosition(OPTIONS_CENTRE[selectedShape]);
        this.addChild(selectedOption, 2);
    },

    onSelectTimeout() {
        this._roundOver = true;

        if (this._cbSelect) {
            this._cbSelect({
                success: false,
                reaction: RoundTimeout * 1000,
                begin: this._begin.getTime(),
                end: new Date().getTime(),
                timeout: true,
            });
        }

        this.addChild(new TimeoutLayer(EXAM_KEY_SHAPE), 4);
    },
});

export default ShapeLayer;



