import LoadScene from './load/LoadScene';
import examManager from './exam';
import AllResource from './resource';
import { setBgColor } from './common/invoke';
import { MAIN_BG_COLOR } from './constant/common';

const DESIGN_RESOLUTION_WIDTH = 1334;
const DESIGN_RESOLUTION_HEIGHT = 750;

window.onload = function () {
    window.addEventListener(
        'message',
        (event) => {
            if (event.data.type === 'fromApp') {
                window.sessionStorage.setItem(
                    'fromApp',
                    JSON.stringify({ isFromApp: true, origin: event.origin }),
                );
            }
        },
        false,
    );


    cc.game.onStart = function () {
        cc.view.setOrientation(cc.ORIENTATION_LANDSCAPE);
        cc.view.setDesignResolutionSize(
            DESIGN_RESOLUTION_WIDTH,
            DESIGN_RESOLUTION_HEIGHT,
            cc.ResolutionPolicy.SHOW_ALL,
        );
        setBgColor(MAIN_BG_COLOR);

        LoadScene.preload(AllResource, () => {
            examManager.start();
        });
    };
    cc.game.run('exam');
};



