import showScene from '../../common/showScene';
import showLollipopExamScene from './LollipopExamScene';
import { RES_LOLLIPOP } from '../../resource/lollipop';
import { EXAM_KEY_LOLLIPOP } from '../../constant/exam';
import { RES_COMMON } from '../../resource/common';
import VoiceManager from '../../common/voiceManager';
import GuideOverLayer from '../../common/GuideOverLayer';
import HeaderLayer from '../base/HeaderLayer';
import { guideConfig, PartDuration, BtnConfirmPosition } from './round';
import HandleUtil from '../base/HandleUtil';
import ErrorTips from '../base/ErrorTips';
import BGLayer from './BGLayer';
import GuideFirstPage from '../base/GuideFirstPage';

const TAG_TIPS_LAYER = 1;
const TAG_BASE_SELECTED_SPRITE = 100;
const TAG_MENU = 1000;
const TAG_BTN_SELECTED = 1001;


const LollipopGuideLayer = cc.Layer.extend({
    _hand: null,
    _config: null,
    _selected: null,
    _step: 0,
    _tips: null,
    ctor() {
        this._super();
        this._hand = null;
        this._config = guideConfig;
        this._selected = [];
        this._step = 0;
        this._tips = null;
        const bgLayer = new BGLayer();
        this.addChild(bgLayer);
        this.showDish();
        const header = new HeaderLayer(
            EXAM_KEY_LOLLIPOP,
            this.onSkip.bind(this),
            null,
            null,
            this.onGuideAgain.bind(this),
        );
        this.addChild(header, 99);
        this.addChild(new GuideFirstPage(EXAM_KEY_LOLLIPOP, 6, new BGLayer()));
        // this.scheduleOnce(this.showTips, PartInterval);
    },
    onEnter() {
        this._super();
    },
    showDish() {
        const menu = new cc.Menu();
        this._hand = new HandleUtil(menu, false);
        this._tips = new ErrorTips(menu);
        menu.setPosition(cc.p(0, 0));

        this._config.position.map((position, index) => {
            const menuItem = new cc.MenuItemImage(RES_LOLLIPOP.dish, null, this.onDishSelected.bind(this));
            menuItem.setPosition(position);
            menuItem.setEnabled(false);
            menu.addChild(menuItem, 0, index);
            return position;
        });

        this.addChild(menu, 0, TAG_MENU);
        this.renderInto();
    },

    onDishSelected(menuItem) {
        this.setDishClick(false);
        const tag = menuItem.getTag();
        const selectedIndex = this._selected.indexOf(tag);
        if (tag === 0 || tag === 3) {
            const p = this._config.position[tag];
            this._tips.hide();
            this._tips.show(cc.p({ x: p.x + 110, y: p.y - 110 }));
            this.playGuideVoice(
                RES_LOLLIPOP.gameError,
                () => {
                    this.setDishClick(true);
                    this._tips.hide();
                },
            );
            return;
        }
        if (this._selected.length === 0) {
            this._hand.hide();
            if (tag === 2 && selectedIndex === -1) {
                this.renderAutoToggle();
            }
            if (tag === 1 && selectedIndex === -1) {
                this.playGuideVoice(
                    RES_LOLLIPOP.selectRight,
                    () => {
                        this.setDishClick(true);
                        const p = this._config.position[2];
                        this._hand.show(cc.p({ x: p.x + 100, y: p.y - 130 }));
                    },
                );
            }
        } else {
            this._hand.hide();
            if (tag === 2 && selectedIndex === -1) {
                this.renderAutoToggle();
            }
            if (tag === 1 && selectedIndex === -1) {
                this.playGuideVoice(RES_LOLLIPOP.selectDoubleRight, this.renderClick.bind(this));
            }
        }

        if (selectedIndex === -1) {
            const selectedSprite = new cc.Sprite(RES_LOLLIPOP.selected);
            selectedSprite.setPosition(this._config.position[tag]);
            this.addChild(selectedSprite, 0, TAG_BASE_SELECTED_SPRITE + tag);
            this._selected.push(tag);
        } else {
            this.removeChildByTag(TAG_BASE_SELECTED_SPRITE + tag);
            this._selected.splice(selectedIndex, 1);
            this.setDishClick(true);
        }
    },

    renderAutoToggle() {
        this.playGuideVoice(RES_LOLLIPOP.selectCancel, () => {
            const selectedIndex = this._selected.indexOf(2);
            const p = this._config.position[2];
            this._hand.show(cc.p({ x: p.x + 100, y: p.y - 130 }));
            this.scheduleOnce(() => {
                this._hand.hide();
            }, 0.8);

            this.scheduleOnce(() => {
                this.removeChildByTag(TAG_BASE_SELECTED_SPRITE + 2);
                this._selected.splice(selectedIndex, 1);
                this.scheduleOnce(() => {
                    this._hand.show(cc.p({ x: p.x + 100, y: p.y - 130 }));
                    this.scheduleOnce(() => {
                        this._hand.hide();
                    }, 0.8);
                }, 1);

                this.scheduleOnce(() => {
                    const selectedSprite = new cc.Sprite(RES_LOLLIPOP.selected);
                    selectedSprite.setPosition(this._config.position[2]);
                    this.addChild(selectedSprite, 0, TAG_BASE_SELECTED_SPRITE + 2);
                    this._selected.push(2);
                    if (this._selected.length === 1) {
                        this.playGuideVoice(RES_LOLLIPOP.selectRight, this.toOtherPlate.bind(this));
                    } else {
                        this.playGuideVoice(RES_LOLLIPOP.gameOtherEnd, this.renderClick.bind(this));
                    }
                }, 2);
            }, 1);
        });
    },

    renderClick() {
        this._hand.show(cc.p({ x: BtnConfirmPosition.x + 70, y: BtnConfirmPosition.y - 60 }));
        this.setBtnClick();
    },

    toOtherPlate() {
        const p = this._config.position[1];
        this._hand.show(cc.p({ x: p.x + 100, y: p.y - 130 }));
        this.setDishClick(true);
    },

    renderInto() {
        this.playGuideVoice(RES_LOLLIPOP.gameIntro, this.showTips.bind(this));
    },

    showTips() {
        if (this._step === 2) {
            return;
        }
        const layer = new cc.Layer();
        const lollipopSprite = new cc.Sprite(this._config.lollipop);
        layer.addChild(lollipopSprite);
        this.addChild(layer, 0, TAG_TIPS_LAYER);
        this.scheduleOnce(this.clearTips, PartDuration);
        if (this._step === 0) {
            lollipopSprite.setPosition(this._config.position[this._config.displayIndex[0][0]]);
            this.playGuideVoice(RES_LOLLIPOP.plateOne, this.showTips.bind(this));
        } else {
            lollipopSprite.setPosition(this._config.position[this._config.displayIndex[1][0]]);
            this.playGuideVoice(RES_LOLLIPOP.plateTwo, this.askQuestion.bind(this));
        }
    },

    clearTips() {
        if (this._step === 2) {
            return;
        }
        if (this.getChildByTag(TAG_TIPS_LAYER)) {
            this.removeChildByTag(TAG_TIPS_LAYER);
        }
        this._step += 1;
    },

    askQuestion() {
        this.addChild(new GuideFirstPage(EXAM_KEY_LOLLIPOP, 8, new BGLayer(), true));
        this.playGuideVoice(RES_LOLLIPOP.gameQuestion, this.showSelect.bind(this));
    },

    showSelect() {
        const menu = this.getChildByTag(TAG_MENU);
        const btnConfirm = new cc.MenuItemImage(
            RES_COMMON.btn_confirm_normal,
            RES_COMMON.btn_confirm_pressed,
            RES_COMMON.btn_confirm_disable,
            this.onConfirmSelected,
            this,
        );

        btnConfirm.setPosition(BtnConfirmPosition);
        btnConfirm.setEnabled(false);
        menu.addChild(btnConfirm, 0, TAG_BTN_SELECTED);
        this.setDishClick(true);

        this.renderFirseAnswer();
    },
    renderFirseAnswer() {
        const p = this._config.position[2];
        this._hand.show(cc.p({ x: p.x + 100, y: p.y - 130 }));
    },
    // onStart() {
    //     showLollipopExamScene();
    // },
    setDishClick(flag) {
        const menu = this.getChildByTag(TAG_MENU);
        this._config.position.map((p, index) => {
            const dish = menu.getChildByTag(index);
            dish.setEnabled(flag);
            return null;
        });
    },
    setBtnClick() {
        const menu = this.getChildByTag(TAG_MENU);
        const btnConfirm = menu.getChildByTag(TAG_BTN_SELECTED);

        if (this._selected.length === 2 && btnConfirm) {
            btnConfirm.setEnabled(true);
        } else {
            btnConfirm.setEnabled(false);
        }
    },

    onConfirmSelected() {
        this._hand.hide();
        // this.playGuideVoice(RES_LOLLIPOP_AUDIO.lollipop_question.path, this.showSelect.bind(this));
        this.guideOver();
    },

    playGuideVoice(path, cb) {
        VoiceManager.playVoice(path, cb);
    },
    onGuideAgain() {
        showLollipopGuideScene();
    },

    guideOver() {
        const overLayer = new GuideOverLayer(
            this.onGuideAgain.bind(this),
            this.onSkip.bind(this),
            EXAM_KEY_LOLLIPOP,
        );
        this.playGuideVoice(RES_LOLLIPOP.gameEnd);
        this.addChild(overLayer, 999);
    },
    onSkip() {
        VoiceManager.stopVoice();
        showLollipopExamScene();
    },
});

export default function showLollipopGuideScene() {
    showScene(new LollipopGuideLayer());
}



