import { RES_CONNECTION } from '../../resource/connection';
import { ErrorDuration } from './round';

const RES_NUMBER = [
    RES_CONNECTION.number_1,
    RES_CONNECTION.number_2,
    RES_CONNECTION.number_3,
    RES_CONNECTION.number_4,
    RES_CONNECTION.number_5,
    RES_CONNECTION.number_6,
    RES_CONNECTION.number_7,
];

const RES_BACKGROUND = [
    RES_CONNECTION.bg_blue,
    RES_CONNECTION.bg_green,
];

const RES_BACKGROUND_SELECTED = [
    RES_CONNECTION.bg_blue_selected,
    RES_CONNECTION.bg_green_selected,
];

const TAG_NUMBER = 1;

const PointSprite = cc.Sprite.extend({
    _type: NaN,
    _number: NaN,
    _numberSprite: null,

    ctor(number, type) {
        this._super(RES_BACKGROUND[type]);

        this._number = number;
        this._type = type;

        const rect = this.getTextureRect();
        this._numberSprite = new cc.Sprite(RES_NUMBER[number]);
        this._numberSprite.setPosition(rect.width / 2, rect.height / 2);
        this.addChild(this._numberSprite, 0, TAG_NUMBER);
    },

    showError() {
        const textureBGRed = cc.textureCache.addImage(RES_CONNECTION.bg_red);
        const rectBGRed = new cc.Rect(0, 0, textureBGRed.width, textureBGRed.height);
        const spriteFrameBGRed = new cc.SpriteFrame(textureBGRed, rectBGRed);
        this.setSpriteFrame(spriteFrameBGRed);

        const textureError = cc.textureCache.addImage(RES_CONNECTION.icon_error);
        const rectError = new cc.Rect(0, 0, textureError.width, textureError.height);
        const spriteFrameError = new cc.SpriteFrame(textureError, rectError);
        this._numberSprite.setSpriteFrame(spriteFrameError);

        this.scheduleOnce(this.recover, ErrorDuration);
    },

    recover() {
        const textureOrigin = cc.textureCache.addImage(RES_BACKGROUND[this._type]);
        const rectOrigin = new cc.Rect(0, 0, textureOrigin.width, textureOrigin.height);
        const spriteFrameOrigin = new cc.SpriteFrame(textureOrigin, rectOrigin);
        this.setSpriteFrame(spriteFrameOrigin);

        const textureNumber = cc.textureCache.addImage(RES_NUMBER[this._number]);
        const rectNumber = new cc.Rect(0, 0, textureNumber.width, textureNumber.height);
        const spriteFrameNumber = new cc.SpriteFrame(textureNumber, rectNumber);
        this._numberSprite.setSpriteFrame(spriteFrameNumber);
    },

    setSelected() {
        const textureSelected = cc.textureCache.addImage(RES_BACKGROUND_SELECTED[this._type]);
        const rectSelected = new cc.Rect(0, 0, textureSelected.width, textureSelected.height);
        const spriteFrameSelected = new cc.SpriteFrame(textureSelected, rectSelected);
        this.setSpriteFrame(spriteFrameSelected);

        this._numberSprite.setOpacity(160);
    },
});

PointSprite.Blue = 0;
PointSprite.Green = 1;

export default PointSprite;



