import showScene from '../../common/showScene';
import showShapeExamScene from './ShapeExamScene';
import VoiceManager from '../../common/voiceManager';
import { RES_SHAPE, RES_SMALL_SHAPE, RES_OPTIONS, RES_SELECTED } from '../../resource/shape';
import { SHAPE_ARRAY, BIG_ROPE_CENTRE, POSITIONS, OPTIONS_CENTRE } from './position';
import GuideOverLayer from '../../common/GuideOverLayer';
import HeaderLayer from '../base/HeaderLayer';
import BGLayer from './BGLayer';
import HandleUtil from '../base/HandleUtil';
import ErrorTips from '../base/ErrorTips';
import GuideFirstPage from '../base/GuideFirstPage';
import { EXAM_KEY_SHAPE } from '../../constant/exam';


const TAG_SELECTED = 999;


const ShapeGuideLayer = cc.Layer.extend({
    _enablePress: null,
    _hand: null,
    _error: null,

    ctor() {
        this._super();
        this._enablePress = false;

        const bgLayer = new BGLayer();
        this.addChild(bgLayer);

        const header = new HeaderLayer(
            EXAM_KEY_SHAPE,
            this.onSkip.bind(this),
            null,
            null,
            this.onGuideAgain.bind(this),
        );
        this.addChild(header, 99);
        const menu = new cc.Menu();
        menu.setPosition(0, 0);
        this._hand = new HandleUtil(menu, false);
        this._error = new ErrorTips(menu, false);
        this.addChild(new GuideFirstPage(EXAM_KEY_SHAPE, 4, new BGLayer()), 3);
        this.addChild(menu, 3);
    },

    onEnter() {
        this._super();
        this.shapeAppear();
        this.playGuideVoice();
    },

    playGuideVoice() {
        VoiceManager.playVoice(RES_SHAPE.guide, this.playTip.bind(this));
    },

    shapeAppear() {
        const bigShape = SHAPE_ARRAY[0];
        const smallShape = SHAPE_ARRAY[1];
        const ropePosition = BIG_ROPE_CENTRE;
        const bigShapePositions = POSITIONS[bigShape];

        for (let i = 0; i < bigShapePositions.length; i += 1) {
            const smallShapeImage = RES_SMALL_SHAPE[smallShape];
            const smallShapeItem = new cc.Sprite(smallShapeImage);
            smallShapeItem.setPosition(bigShapePositions[i]);
            this.addChild(smallShapeItem, 1, i);
        }

        const ropeImage = RES_SHAPE.big_rope;
        const rope = new cc.Sprite(ropeImage);
        rope.setPosition(ropePosition);

        this.addChild(rope, 2);

        const menu = new cc.Menu();
        menu.setPosition(0, 0);

        for (let i = 0; i < OPTIONS_CENTRE.length; i += 1) {
            const menuItem = new cc.MenuItemImage(RES_OPTIONS[i], null, this.onOptionPressed, this);
            menuItem.setPosition(OPTIONS_CENTRE[i]);
            menu.addChild(menuItem, 1, i);
        }

        this.addChild(menu);
    },

    playTip() {
        VoiceManager.playVoice(RES_SHAPE.tip, this.enablePress.bind(this));
    },

    enablePress() {
        this._enablePress = true;
        this._hand.show(cc.p(OPTIONS_CENTRE[0].x + 90, OPTIONS_CENTRE[0].y - 83));
    },

    onOptionPressed(menuItem) {
        if (!this._enablePress) {
            return;
        }
        this._enablePress = false;

        const selectedShape = menuItem.getTag();
        this.selectedAppear(selectedShape);
        const isCorrect = selectedShape === SHAPE_ARRAY[0];
        this._error.hide();

        if (isCorrect) {
            VoiceManager.playVoice(RES_SHAPE.correct);
            this.guideOver();
        } else {
            this._error.show(cc.p(menuItem.x + 50, menuItem.y - 50));
            VoiceManager.playVoice(RES_SHAPE.incorrect, this.selectedDisappear.bind(this));
        }
    },

    selectedAppear(selectedShape) {
        const selectedOption = new cc.Sprite(RES_SELECTED[selectedShape]);
        selectedOption.setPosition(OPTIONS_CENTRE[selectedShape]);
        this.addChild(selectedOption, 2, TAG_SELECTED);
    },

    selectedDisappear() {
        if (this.getChildByTag(TAG_SELECTED)) {
            this.removeChildByTag(TAG_SELECTED);
        }
        this._error.hide();
        this._enablePress = true;
    },

    onGuideAgain() {
        showShapeGuideScene();
    },

    guideOver() {
        const overLayer = new GuideOverLayer(
            this.onGuideAgain.bind(this),
            this.onSkip.bind(this),
            EXAM_KEY_SHAPE,
        );
        this.addChild(overLayer, 999);
    },

    onSkip() {
        VoiceManager.stopVoice();
        showShapeExamScene();
    },
});

export default function showShapeGuideScene() {
    showScene(new ShapeGuideLayer());
}



