import showScene from '../../common/showScene';
import showNumberExamScene from './NumberExamScene';
import { EXAM_KEY_NUMBER } from '../../constant/exam';
import VoiceManager from '../../common/voiceManager';
import NumberSprite from './NumberSprite';
import { RES_NUMBER } from '../../resource/number';
import SelectLayer from './SelectLayer';
import GuideFirstPage from '../base/GuideFirstPage';
import HeaderLayer from '../base/HeaderLayer';
import BGLayer from './BGLayer';
import GuideOverLayer from '../../common/GuideOverLayer';

const TAG_SINGLE_NUMBER_LAYER = 2;

const NumberGuideLayer = cc.Layer.extend({
    _numbers: null,
    _step: 0,
    ctor() {
        this._super();

        this._numbers = [4, 3, 7];
        this._step = 0;

        this.addChild(new BGLayer());
        VoiceManager.playVoice(RES_NUMBER.number_audio_2);
        this.addChild(new GuideFirstPage(EXAM_KEY_NUMBER, 21, new BGLayer()));
        this.addChild(
            new HeaderLayer(
                EXAM_KEY_NUMBER,
                this.onStart.bind(this),
                false,
                null,
                this.onGuideAgain.bind(this),
            ),
            99,
        );
        this.scheduleOnce(this.titleDisappear, 21);
    },
    showNumber() {
        const winSize = cc.director.getWinSize();
        const numberLayer = new NumberSprite(this._numbers[this._step], NumberSprite.Small, true);
        numberLayer.setPosition(winSize.width / 2, winSize.height / 2);
        this.addChild(numberLayer, 0, TAG_SINGLE_NUMBER_LAYER);
        if (this._step === 0) {
            VoiceManager.playVoice(RES_NUMBER.number_audio_4);
        } else if (this._step === 1) {
            VoiceManager.playVoice(RES_NUMBER.number_audio_5);
        } else {
            VoiceManager.playVoice(RES_NUMBER.number_audio_6);
        }
        this.scheduleOnce(this.hideNumber, 3.5);
    },
    hideNumber() {
        if (this.getChildByTag(TAG_SINGLE_NUMBER_LAYER)) {
            this.removeChildByTag(TAG_SINGLE_NUMBER_LAYER);
        }

        this._step += 1;

        if (this._step < this._numbers.length) {
            this.scheduleOnce(this.showNumber, 1);
        } else {
            this.scheduleOnce(this.showSelect, 1);
        }
    },
    showSelect() {
        this.addChild(new SelectLayer(
            this._numbers,
            null,
            true,
            this.GuideOverLayer.bind(this),
        ));
    },
    titleDisappear() {
        this.showNumber();
    },
    GuideOverLayer() {
        const overLayer = new GuideOverLayer(
            this.onGuideAgain.bind(this),
            this.onStart.bind(this),
            EXAM_KEY_NUMBER,
        );
        this.addChild(overLayer, 999);
    },
    onGuideAgain() {
        showNumberGuideScene();
    },
    onStart() {
        VoiceManager.stopVoice();
        showNumberExamScene();
    },
});

export default function showNumberGuideScene() {
    showScene(new NumberGuideLayer());
}



