import {
    EXAM_KEY_CANDY,
    EXAM_KEY_APPLE,
    EXAM_KEY_LOLLIPOP,
    EXAM_KEY_NUMBER,
    EXAM_KEY_PAINTER,
    EXAM_KEY_PET,
    EXAM_KEY_CONNECTION,
    EXAM_KEY_SHAPE,
} from '../constant/exam';
import startCandy from './candy';
import startApple from './apple';
import startLollipop from './lollipop';
import startNumber from './number';
import startPainter from './painter';
import startPet from './pet';
import startConnection from './connection';
import startShape from './shape';

const DAY_PER_YEAR = 365;

export const ExamRunners = {
    [EXAM_KEY_CANDY]: startCandy,
    [EXAM_KEY_APPLE]: startApple,
    [EXAM_KEY_LOLLIPOP]: startLollipop,
    [EXAM_KEY_NUMBER]: startNumber,
    [EXAM_KEY_PAINTER]: startPainter,
    [EXAM_KEY_PET]: startPet,
    [EXAM_KEY_CONNECTION]: startConnection,
    [EXAM_KEY_SHAPE]: startShape,
};

export default function generateExamList(day) {
    if (day > Math.floor(DAY_PER_YEAR * 2.5) && day <= DAY_PER_YEAR * 4) {
        return [
            ExamRunners[EXAM_KEY_CANDY],
            ExamRunners[EXAM_KEY_APPLE],
            ExamRunners[EXAM_KEY_LOLLIPOP],
            ExamRunners[EXAM_KEY_NUMBER],
            ExamRunners[EXAM_KEY_PAINTER],
            ExamRunners[EXAM_KEY_PET],
            ExamRunners[EXAM_KEY_CONNECTION],
            ExamRunners[EXAM_KEY_SHAPE],
        ];
    }

    return [
        ExamRunners[EXAM_KEY_CANDY],
        ExamRunners[EXAM_KEY_APPLE],
        ExamRunners[EXAM_KEY_LOLLIPOP],
        ExamRunners[EXAM_KEY_NUMBER],
        ExamRunners[EXAM_KEY_PAINTER],
        ExamRunners[EXAM_KEY_PET],
        ExamRunners[EXAM_KEY_CONNECTION],
        ExamRunners[EXAM_KEY_SHAPE],
    ];
}



