import { RES_CANDY } from '../../resource/candy';
import showIntroScene from '../base/IntroScene';
import showCandyGuideScene from './CandyGuideScene';
import BGLayer from './BGLayer';
import { EXAM_KEY_CANDY } from '../../constant/exam';

export default function () {
    showIntroScene(
        {
            bgLayer: new BGLayer(),
            intro: RES_CANDY.intro_img,
            dx: 36,
            btn: RES_CANDY.btn_img,
            mute: RES_CANDY.btn_mute,
            unmute: RES_CANDY.btn_unmute,
            quit: RES_CANDY.btn_skip,
            voice: RES_CANDY.candyStart,
        },
        showCandyGuideScene,
        EXAM_KEY_CANDY,
    );
}



