import showScene from '../../common/showScene';
import showCandyExamScene from './CandyExamScene';
import { EXAM_KEY_CANDY } from '../../constant/exam';
import { RoundCount } from './round';
import { RES_CANDY } from '../../resource/candy';
import BGLayer from './BGLayer';
import GuideOverLayer from '../../common/GuideOverLayer';
import VoiceManager from '../../common/voiceManager';
import HandleUtil from '../base/HandleUtil';
import ErrorTips from '../base/ErrorTips';
import HeaderLayer from '../base/HeaderLayer';
import GuideFirstPage from '../base/GuideFirstPage';

const CORRECT_TAG = 0;
const candyPosition = [
    cc.p(563, 288),
    cc.p(111, 460),
    cc.p(256, 178),
    cc.p(865, 363),
];
const CandyGuideLayer = cc.Layer.extend({
    _onpress: false,
    _hand: null,
    _error: null,
    ctor() {
        this._super();
        this._onpress = false;
        const menu = new cc.Menu();
        menu.setPosition(0, 0);
        this._hand = new HandleUtil(menu, false);
        this._error = new ErrorTips(menu, false);
        this.addChild(menu, 9);
        this.addChild(new BGLayer());
        this.addChild(new GuideFirstPage(EXAM_KEY_CANDY, 6, new BGLayer()));
        this.header = new HeaderLayer(
            EXAM_KEY_CANDY,
            this.onStart.bind(this),
            false,
            false,
            this.onGuideAgain.bind(this),
            {
                unmuteImage: RES_CANDY.btn_unmute,
                muteImage: RES_CANDY.btn_mute,
            },
        );
        this.addChild(this.header, 99);

        VoiceManager.playVoice(RES_CANDY.candyGuide_1, this.candyGuide_2.bind(this));
        this.scheduleOnce(this.showCandyGuideLayer, 6);
    },
    showCandyGuideLayer() {
        const menu = new cc.Menu();
        menu.setPosition(0, 0);

        const yellowMenuItem = new cc.MenuItemImage(RES_CANDY.candy_yellow, null, this.onCandyPressed, this);
        yellowMenuItem.setPosition(candyPosition[0]);
        yellowMenuItem.setAnchorPoint(cc.p(0, 0));
        menu.addChild(yellowMenuItem, 0, 0);

        for (let i = 1; i < RoundCount[1]; i += 1) {
            const redMenuItem = new cc.MenuItemImage(RES_CANDY.candy_red, null, this.onCandyPressed, this);
            redMenuItem.setPosition(candyPosition[i]);
            redMenuItem.setAnchorPoint(cc.p(0, 0));
            menu.addChild(redMenuItem, 0, i);
        }
        this.addChild(menu);
    },
    candyGuide_2() {
        VoiceManager.playVoice(RES_CANDY.candyGuide_2, this.onMoveHand.bind(this));
    },
    onMoveHand() {
        this._hand.show(cc.p(720, 280));
        this._onpress = true;
    },
    onCandyPressed(menuItem) {
        if (!this._onpress) {
            return;
        }
        const tag = menuItem.getTag();

        if (tag === CORRECT_TAG) {
            VoiceManager.playVoice(RES_CANDY.candyGuideSuss);
            this._hand.hide();
            const textureSelected = cc.textureCache.addImage(RES_CANDY.candy_yellow_selected);
            const selectRect = new cc.Rect(0, 0, textureSelected.width, textureSelected.height);
            const selectFrame = new cc.SpriteFrame(textureSelected, selectRect);
            menuItem.getNormalImage().setSpriteFrame(selectFrame);
            this.guideOver();
            this._onpress = false;
            this._error.hide();
        } else {
            this._error.hide();
            this._error.show(cc.p(menuItem.x + 190, menuItem.y - 16));
            VoiceManager.playVoice(RES_CANDY.candyGuideError, () => {
                this._error.hide();
            });
        }
    },
    guideOver() {
        const overLayer = new GuideOverLayer(
            this.onGuideAgain.bind(this),
            this.onStart.bind(this),
            EXAM_KEY_CANDY,
        );
        this.addChild(overLayer, 999);
    },

    onGuideAgain() {
        showCandyGuideScene();
    },
    onStart() {
        showCandyExamScene();
        VoiceManager.stopVoice();
    },
});

export default function showCandyGuideScene() {
    showScene(new CandyGuideLayer());
}



