import {
    POSITION_FOR_PET_TWO,
    POSITION_FOR_PET_FOUR,
    GuideTime,
} from './round';
import TimeoutLayer from '../../common/TimeoutLayer';
import { EXAM_KEY_PET } from '../../constant/exam';

const PET_FIRST = 1;
const PET_SECOND = 2;
const PET_THIRE = 3;
const PetLayer = cc.Layer.extend({
    _cbSelect: null,
    _begin: null,
    _petNumber: 0,
    _answer: null,
    _arrAnswer: [],
    _roundBegin: null,
    ctor(config, cbSelect) {
        this._super();
        this._config = config;
        this._cbSelect = cbSelect;
        // this.showPet();
        this.scheduleOnce(this.showPet, 0.5);
        this.showNest();
    },
    showNest() {
        const menu = new cc.Menu();
        menu.setPosition(0, 0);
        const {
            nestPosition, nestIndex,
        } = this._config;

        let home = '';
        for (let i = 0; i < nestPosition.length; i += 1) {
            if (nestPosition.length > 2) {
                home = new cc.MenuItemImage(nestIndex[i], null, this.onSelect.bind(this, i), this);
            } else {
                home = new cc.MenuItemImage(nestIndex[0][i], null, this.onSelect.bind(this, i), this);
            }
            home.setPosition(nestPosition[i]);
            home.setAnchorPoint(cc.p(0, 0));

            menu.addChild(home, 0, i);
        }

        this.addChild(menu);
    },
    showPet() {
        const menu = new cc.Menu();
        menu.setPosition(0, 0);
        const { pet, randomTwoOrder, randomFourOrder } = this._config;
        let petImage = '';
        if (pet.length > 2 && this._petNumber === 0) {
            petImage = new cc.MenuItemImage(pet[0][0][randomFourOrder[this._petNumber]], null, null, this);
        } else if (pet.length > 2 && this._petNumber > 0) {
            petImage = new cc.MenuItemImage(
                pet[0][1][this._petNumber - 1][randomFourOrder[this._petNumber]],
                null,
                null,
                this,
            );
            if (this._petNumber === 2) {
                this._roundBegin = new Date();
            }
        } else if (this._petNumber === 2) {
            petImage = new cc.MenuItemImage(
                pet[0][randomTwoOrder[0] - 1][randomFourOrder[this._petNumber]],
                null,
                null,
                this,
            );
            this._answer = randomTwoOrder[0] - 1;
            this._roundBegin = new Date();
        } else {
            petImage = new cc.MenuItemImage(
                pet[0][this._petNumber][randomFourOrder[this._petNumber]],
                null,
                this.onSelect.bind(this),
                this,
            );
        }
        if (pet.length > 2) {
            petImage.setPosition(POSITION_FOR_PET_FOUR);
        } else {
            petImage.setPosition(POSITION_FOR_PET_TWO);
        }
        // const ScaleTo = cc.scaleTo(0, 0, 0);
        const ScaleTos = cc.scaleTo(1, 1, 1);
        petImage.setScale(0.0);
        petImage.runAction(ScaleTos);

        // petImage.runAction(ScaleTos);
        menu.addChild(petImage, 0, 0);
        if (this._petNumber === 2) {
            this.addChild(menu, 0, PET_THIRE);
        } else {
            this.addChild(menu, 0, PET_FIRST);
        }
        if (this._petNumber < 2) {
            this.scheduleOnce(this.petMove, 2);
        } else {
            this._petNumber = 0;
        }
    },

    petMove() {
        const {
            pet, nestPosition, randomFourOrder,
        } = this._config;
        if (this.getChildByTag(PET_FIRST)) {
            this.removeChildByTag(PET_FIRST);
        }
        if (this._petNumber < 2) {
            let petImage = '';
            if (pet.length > 2 && this._petNumber === 0) {
                petImage = new cc.Sprite(pet[0][0][randomFourOrder[this._petNumber]]);
            } else if (pet.length > 2 && this._petNumber > 0) {
                petImage = new cc.Sprite(pet[0][1][this._petNumber - 1][randomFourOrder[this._petNumber]]);
            } else {
                petImage = new cc.Sprite(pet[0][this._petNumber][randomFourOrder[this._petNumber]]);
            }
            if (nestPosition.length > 2) {
                petImage.setPosition(POSITION_FOR_PET_FOUR);
                let moveTo = '';
                if (this._petNumber === 0 && (randomFourOrder[0] === 0 || randomFourOrder[0] === 2)) {
                    this._arrAnswer.push(1, 3);
                } else if (this._petNumber === 0 && (randomFourOrder[0] === 1 || randomFourOrder[0] === 3)) {
                    this._arrAnswer.push(0, 2);
                }
                if ((this._petNumber === 1 && randomFourOrder[0] === 1 && randomFourOrder[1] === 3)
                || (this._petNumber === 1 && randomFourOrder[0] === 3 && randomFourOrder[1] === 1)
                || (this._petNumber === 1 && randomFourOrder[0] === 0 && randomFourOrder[1] === 2)
                || (this._petNumber === 1 && randomFourOrder[0] === 2 && randomFourOrder[1] === 0)) {
                    moveTo = cc.moveTo(1, cc.p(nestPosition[randomFourOrder[2]].x
                      + 150, nestPosition[randomFourOrder[2]].y + 230));
                    for (let i = 0; i < this._arrAnswer.length; i += 1) {
                        if (this._arrAnswer[i] === randomFourOrder[2]) {
                            this._arrAnswer.splice(i, 1);
                            break;
                        }
                    }
                } else if (this._petNumber === 1) {
                    moveTo = cc.moveTo(1, cc.p(nestPosition[randomFourOrder[this._petNumber]].x
                      + 150, nestPosition[randomFourOrder[this._petNumber]].y + 230));
                    for (let i = 0; i < this._arrAnswer.length; i += 1) {
                        if (this._arrAnswer[i] === randomFourOrder[this._petNumber]) {
                            this._arrAnswer.splice(i, 1);
                            break;
                        }
                    }
                } else {
                    moveTo = cc.moveTo(1, cc.p(nestPosition[randomFourOrder[this._petNumber]].x
                      + 150, nestPosition[randomFourOrder[this._petNumber]].y + 230));
                }
                const ScaleTo = cc.scaleTo(1, 0.8, 0.8);
                petImage.runAction(moveTo);
                petImage.runAction(ScaleTo);
            } else {
                petImage.setPosition(POSITION_FOR_PET_TWO);
                const moveTo = cc.moveTo(1, cc.p(nestPosition[this._petNumber].x
                  + 150, nestPosition[this._petNumber].y + 250));
                const ScaleTo = cc.scaleTo(1, 0.8, 0.8);
                petImage.runAction(moveTo);
                petImage.runAction(ScaleTo);
            }
            this.addChild(petImage, 0, PET_SECOND);
            this.scheduleOnce(this.showPet, 2.5);
        }
        this._petNumber += 1;
    },
    onEnter() {
        const { timeout } = this._config;
        this._super();
        this._begin = new Date();
        this.scheduleOnce(this.onSelectTimeout, timeout);
    },
    onSelect(i) {
        const {
            pet, nestPosition, randomTwoOrder, randomFourOrder, timeout,
        } = this._config;
        if (this.getChildByTag(PET_THIRE) &&
        (new Date().getTime() - this._roundBegin.getTime()) < (timeout - GuideTime) * 999) {
            this.removeChildByTag(PET_THIRE);

            if (this._petNumber < 2) {
                const menu = new cc.Menu();
                menu.setPosition(0, 0);
                let petImage = '';
                if (pet.length > 2 && this._petNumber === 0) {
                    petImage = new cc.MenuItemImage(
                        pet[0][1][1][randomFourOrder[2]],
                        null,
                        null,
                        this,
                    );
                } else {
                    petImage = new cc.MenuItemImage(
                        pet[0][2 - [randomTwoOrder[1]]][randomFourOrder[2]],
                        null,
                        null,
                        this,
                    );
                }
                if (nestPosition.length > 2) {
                    petImage.setPosition(POSITION_FOR_PET_FOUR);
                    const moveTo = cc.moveTo(1, cc.p(nestPosition[i].x + 320, nestPosition[i].y + 230));
                    const ScaleTo = cc.scaleTo(1, 0.8, 0.8);
                    petImage.runAction(moveTo);
                    petImage.runAction(ScaleTo);
                } else {
                    petImage.setPosition(POSITION_FOR_PET_TWO);
                    const moveTo = cc.moveTo(1, cc.p(nestPosition[i].x + 320, nestPosition[i].y + 250));
                    const ScaleTo = cc.scaleTo(1, 0.8, 0.8);
                    petImage.runAction(moveTo);
                    petImage.runAction(ScaleTo);
                }
                menu.addChild(petImage, 0, 0);
                this.addChild(menu, 0, PET_SECOND);
            }
            const result = {
                success: nestPosition.length > 2 ? i === this._arrAnswer[0] : this._answer === i,
                reaction: new Date().getTime() - this._roundBegin.getTime(),
                timeout: false,
            };
            if (this._cbSelect) {
                this._cbSelect(result);
                this._arrAnswer.splice(0, this._arrAnswer.length);
            }
        }
    },

    onSelectTimeout() {
        const { timeout } = this._config;
        if (this._cbSelect) {
            this._cbSelect({
                success: false,
                reaction: (timeout - GuideTime) * 1000,
                timeout: true,
            });
            this._arrAnswer.splice(0, this._arrAnswer.length);
        }

        this.addChild(new TimeoutLayer(EXAM_KEY_PET));
    },
});

export default PetLayer;



