import { RES_PET } from '../../resource/pet';
import showIntroScene from '../base/IntroScene';
import showPetGuideScene from './PetGuideScene';
import BGLayer from './BGLayer';
import { EXAM_KEY_PET } from '../../constant/exam';

export default function () {
    showIntroScene(
        {
            bgLayer: new BGLayer(),
            intro: RES_PET.intro_img,
            btn: RES_PET.btn_img,
            voice: RES_PET.pet_1,
        },
        showPetGuideScene,
        EXAM_KEY_PET,
    );
}



