import NumberSprite from './NumberSprite';
import SelectLayer from './SelectLayer';
import { PartDuration, PartInterval } from './round';

const TAG_SINGLE_NUMBER_LAYER = 1;

const NumberLayer = cc.Layer.extend({
    _numbers: null,
    _cbDone: null,
    _step: 0,

    ctor(numbers, cbDone) {
        this._super();

        this._numbers = numbers;
        this._cbDone = cbDone;
        this._step = 0;

        this.showNumber();
    },

    showNumber() {
        const winSize = cc.director.getWinSize();
        const numberLayer = new NumberSprite(this._numbers[this._step], NumberSprite.Small, true);
        numberLayer.setPosition(winSize.width / 2, winSize.height / 2);
        this.addChild(numberLayer, 0, TAG_SINGLE_NUMBER_LAYER);

        this.scheduleOnce(this.hideNumber, PartDuration);
    },

    hideNumber() {
        if (this.getChildByTag(TAG_SINGLE_NUMBER_LAYER)) {
            this.removeChildByTag(TAG_SINGLE_NUMBER_LAYER);
        }

        this._step += 1;

        if (this._step < this._numbers.length) {
            this.scheduleOnce(this.showNumber, PartInterval);
        } else {
            this.scheduleOnce(this.showSelect, PartInterval);
        }
    },

    showSelect() {
        this.addChild(new SelectLayer(this._numbers, this._cbDone));
    },

});

export default NumberLayer;



