import { CATEGORY_KEY_COGNITION } from '../../constant/exam';
import configManager from '../../config';
import calcCognition from './catetory/cognition';

export default function calculate(result, begin, end) {
    const { abilitys, totalTime } = calcCognition(result);

    return {
        age_days: configManager.getBabyAge(),
        baby_id: configManager.getBabyId(),
        category: CATEGORY_KEY_COGNITION,
        begin,
        end,
        total_time: totalTime,
        exam_data: { ...abilitys },
    };
}



