import {
    EXAM_KEY_APPLE,
    LEVEL1,
    LEVEL2,
    LEVEL3,
    INVALID_REACTION,
} from '../../../constant/exam';
import configManager from '../../../config';

const SCORE_PER_WRONG = 5;


function calcAppleLevelScore(round1, round2, round3, round4, level) {
    const levelConfig = configManager.getLevelConfig(EXAM_KEY_APPLE, level.index);

    const result = { items: [round1, round2, round3, round4], level: level.value };

    let allWrong = true;

    for (let i = 0; i < result.items.length; i += 1) {
        if (result.items[i].success) {
            allWrong = false;
            break;
        }
    }

    if (allWrong) {
        result.score = 0;
        result.original_score = 0;
        result.average_reaction = INVALID_REACTION;
        result.accuracy = 0;

        return result;
    }

    let correctCount = 0;
    let correctTotalReaction = 0;
    let deductScore = 0;

    result.items.map((item) => {
        if (item.success) {
            correctCount += 1;
            correctTotalReaction += item.reaction;
        } else {
            deductScore += SCORE_PER_WRONG;
        }

        return item;
    });

    const averageReaction = correctTotalReaction / correctCount;

    let score = (((levelConfig.reaction_threshold - averageReaction) / levelConfig.standard_deviation) * 10) + 50;

    score -= deductScore;

    result.original_score = score;
    result.score = score;
    if (score > levelConfig.max_score) {
        result.score = levelConfig.max_score;
    }
    if (score < 0) {
        result.score = 0;
    }
    result.accuracy = correctCount / result.items.length;
    result.average_reaction = averageReaction;

    return result;
}

export default function calcApple(result) {
    const { begin, end, rounds } = result[EXAM_KEY_APPLE];

    const level1 = calcAppleLevelScore(rounds[0], rounds[1], rounds[2], rounds[3], LEVEL1);
    const level2 = calcAppleLevelScore(rounds[4], rounds[5], rounds[6], rounds[7], LEVEL2);
    const level3 = calcAppleLevelScore(rounds[8], rounds[9], rounds[10], rounds[11], LEVEL3);
    const score = (level1.score * 0.2) + (level2.score * 0.3) + (level3.score * 0.5);


    return {
        score,
        begin,
        end,
        levels: [level1, level2, level3],
    };
}



