import { setBgColor } from '../common/invoke';
import { EXAM_BG_COLORS_STRING_BY_KEY, EXAM_MODAL_BG_COLORS_STRING_BY_KEY } from '../constant/common';

const ModalLayer = cc.Layer.extend({

    ctor(cbLeft, cbRight, examKey, { bgImage, leftBtnImage, rightBtnImage }) {
        this._super();

        this._cbLeft = cbLeft;
        this._cbRight = cbRight;
        this._bgImage = bgImage;
        this._bgColor = EXAM_BG_COLORS_STRING_BY_KEY[examKey];
        this._leftBtnImage = leftBtnImage;
        this._rightBtnImage = rightBtnImage;
        const menu = new cc.Menu();
        const { width, height } = cc.director.getWinSize();
        menu.setPosition(0, 0);

        setBgColor(EXAM_MODAL_BG_COLORS_STRING_BY_KEY[examKey]);

        const bgLayer = new cc.LayerColor(cc.color(77, 77, 73, 242));
        this.addChild(bgLayer, 2, 1);

        this.addChild(menu, 4, 1);

        const bg = new cc.Sprite(this._bgImage);
        bg.setPosition(width / 2, height - 300);
        this.addChild(bg, 3);


        const btnGuideAgain = new cc.MenuItemImage(
            this._leftBtnImage, null,
            this.onLeftBtnPressed, this,
        );
        btnGuideAgain.setPosition(420, 100);
        menu.addChild(btnGuideAgain);

        const btnStart = new cc.MenuItemImage(this._rightBtnImage, null, this.onRightBtnPressed, this);
        btnStart.setPosition(920, 100);
        menu.addChild(btnStart);
    },

    onEnter() {
        this._super();

        this._listener = cc.eventManager.addListener(
            {
                event: cc.EventListener.TOUCH_ONE_BY_ONE,
                swallowTouches: true,
                onTouchBegan: function () { return true; },
                onTouchMoved: function () { return true; },
                onTouchEnded: function () { return true; },
            },
            this,
        );
    },

    onExit() {
        cc.eventManager.removeListener(this._listener);
        setBgColor(this._bgColor);

        this._super();
    },

    onLeftBtnPressed() {
        if (this._cbLeft) {
            this._cbLeft();
        }
    },

    onRightBtnPressed() {
        if (this._cbRight) {
            this._cbRight();
        }
    },
});


export default ModalLayer;



