import { RES_COMMON } from '../resource/common';

const LoadScene = cc.Scene.extend({
    _progressLable: null,
    _resource: null,
    _cbDone: null,

    ctor(resource, cbDone) {
        this._super();

        this._resource = resource;
        this._cbDone = cbDone;

        const winSize = cc.director.getWinSize();
        const bgLayer = new cc.LayerColor(cc.color(244, 255, 255));

        this.addChild(bgLayer);
        this.onLoading();
        this._progressLable = new cc.LabelTTF('0.00%', '', 60);
        this._progressLable.setColor(cc.color(0, 157, 148));
        this._progressLable.setAnchorPoint(cc.p(0.5, 0.5));
        this._progressLable.setPosition(winSize.width / 2, (winSize.height / 2) - 50);
        bgLayer.addChild(this._progressLable);
    },

    onEnter() {
        this._super();

        this.scheduleOnce(this._startLoading, 0.3);
    },
    onLoading() {
        const winSize = cc.director.getWinSize();
        let px = 590;
        const dx = 35;
        [1, 2, 3].forEach((x, index) => {
            const dot = new cc.Sprite(RES_COMMON.loading);
            dot.setPosition(cc.p(px += dx, (winSize.height / 2) + 50));
            const action = cc.sequence(
                cc.fadeOut(0),
                cc.delayTime((index + 1) * 0.4),
                cc.fadeIn(0),
                cc.delayTime((3 - index) * 0.4),
            );
            this.addChild(dot, 9);
            dot.runAction(action.repeatForever());
        });
    },
    _startLoading() {
        this.unschedule(this._startLoading);
        cc.loader.load(
            this._resource,
            (result, count, loadedCount) => {
                let percent = (loadedCount / count) * 100;
                percent = Math.min(percent, 100).toFixed(2, 10);
                this._progressLable.setString(`${percent}%`);
            },
            () => {
                this._progressLable.setString('100%');
                this.scheduleOnce(this._cbDone, 0.3);
            },
        );
    },
});

LoadScene.preload = function (resource, cbDone) {
    const loadScene = new LoadScene(resource, cbDone);
    cc.director.runScene(loadScene);
};

export default LoadScene;



