import TimeoutLayer from '../../common/TimeoutLayer';
import PointSprite from './PointSprite';
import { EXAM_KEY_CONNECTION } from '../../constant/exam';

const TAG_MENU = 1;
const ORDER_MENU = 1;
const ORDER_TIMEOUTLAYER = 2;
const TAG_STAFF_BASE = 100;

const COLOR = [PointSprite.Blue, PointSprite.Green];

class ColorTypeSelector {
    constructor() {
        this.index = Math.floor(COLOR.length * Math.random());
    }

    next() {
        const type = COLOR[this.index];

        this.index += 1;

        if (this.index === COLOR.length) {
            this.index = 0;
        }

        return type;
    }
}

const ConnectionLayer = cc.Layer.extend({
    _config: null,
    _cbDone: null,
    _currentIndex: 0,
    _begin: null,
    _userActions: null,

    ctor(config, cbDone) {
        this._super();

        this._config = config;
        this._cbDone = cbDone;
        this._userActions = [];
        this._drawNode = new cc.DrawNode();
        this.addChild(this._drawNode);

        this.showPoints();
    },

    onEnter() {
        this._super();

        this.scheduleOnce(this.onRoundTimeout, this._config.timeout);
        this._begin = new Date();
    },

    showPoints() {
        const { points: { correct, staff } } = this._config;

        const menu = new cc.Menu();
        menu.setPosition(0, 0);
        this.addChild(menu, ORDER_MENU, TAG_MENU);

        const colorSelector = new ColorTypeSelector();

        correct.map((position, index) => {
            const type = colorSelector.next();

            const correctSprite = new PointSprite(index, type);
            const correctItem = new cc.MenuItemSprite(correctSprite, null, this.onPointTouch, this);
            correctItem.setPosition(position);

            menu.addChild(correctItem, 0, index);

            // if (index === 0) {
            //     correctItem.setEnabled(false);
            // }

            if (index !== correct.length - 1) {
                const staffSprite = new PointSprite(index + 1, type);
                const staffItem = new cc.MenuItemSprite(staffSprite, null, this.onPointTouch, this);
                staffItem.setPosition(staff[index]);
                menu.addChild(staffItem, 0, TAG_STAFF_BASE + index);
            }

            return position;
        });
    },

    onPointTouch(item) {
        const tag = item.getTag();

        this._userActions.push({
            time: new Date(),
            index: tag,
        });

        if (tag >= TAG_STAFF_BASE || tag !== this._currentIndex) {
            item.getNormalImage().showError();
        } else {
            item.setEnabled(false);
            item.getNormalImage().setSelected();

            if (this._currentIndex !== 0) {
                this.drawLines();
            }

            this._currentIndex += 1;

            if (this._currentIndex === this._config.points.correct.length) {
                this.onRoundFinish();
            }
        }
    },

    drawLines() {
        this._drawNode.drawSegment(
            this._config.points.correct[this._currentIndex],
            this._config.points.correct[this._currentIndex - 1],
            3,
            cc.color(8, 172, 162),
        );
    },

    onRoundTimeout() {
        this.addChild(new TimeoutLayer(EXAM_KEY_CONNECTION), ORDER_TIMEOUTLAYER);

        this.onRoundFinish();
    },

    onRoundFinish() {
        if (this._cbDone) {
            this._cbDone({
                count: this._config.points.correct.length,
                correct: this._currentIndex,
                reaction: new Date().getTime() - this._begin.getTime(),
                userActions: this._userActions,
            });
        }
    },
});

export default ConnectionLayer;



