import showScene from '../../common/showScene';
import showPetExamScene from './PetExamScene';
import { EXAM_KEY_PET } from '../../constant/exam';
import VoiceManager from '../../common/voiceManager';
import { RES_PET } from '../../resource/pet';
import { RES_COMMON } from '../../resource/common';
import GuideOverLayer from '../../common/GuideOverLayer';
import HeaderLayer from '../base/HeaderLayer';
import GuideFirstPage from '../base/GuideFirstPage';
import ErrorTips from '../base/ErrorTips';
import BGLayer from './BGLayer';

const PET = [
    RES_PET.cat_black_1,
    RES_PET.cat_yellow_3,
    RES_PET.cat_yellow_2,
];
const POSITION = [
    cc.p(3, 30),
    cc.p(820, 30),
];
const NEST = [
    RES_PET.nest_round_1,
    RES_PET.nest_square_1,
];
const POSITION_FOR_PET = cc.p(659, 524);

const PetGuideLayer = cc.Layer.extend({
    _petNumber: 0,
    _onpress: false,
    _error: null,
    ctor() {
        this._super();
        this._petNumber = 0;
        this._onpress = false;

        VoiceManager.playVoice(RES_PET.pet_2);
        this.addChild(new BGLayer());
        this.addChild(new GuideFirstPage(EXAM_KEY_PET, 26, new BGLayer()));

        this.addChild(
            new HeaderLayer(
                EXAM_KEY_PET,
                this.onStart.bind(this),
                null,
                null,
                this.onGuideAgain.bind(this),
            ),
            99,
        );

        this.scheduleOnce(this.showNest, 26);
    },
    showNest() {
        VoiceManager.playVoice(RES_PET.pet_3);
        const menu = new cc.Menu();
        this._error = new ErrorTips(menu, false);
        menu.setPosition(0, 0);
        let home = '';
        for (let i = 0; i < 2; i += 1) {
            home = new cc.MenuItemImage(NEST[i], null, this.onSelect.bind(this, i), this);
            home.setPosition(POSITION[i]);
            home.setAnchorPoint(cc.p(0, 0));

            menu.addChild(home, 0, i);
        }
        this.addChild(menu);
        this.scheduleOnce(this.showPet, 7);
    },
    showPet() {
        if (this._petNumber === 0) {
            VoiceManager.playVoice(RES_PET.pet_4);
        } else if (this._petNumber === 1) {
            VoiceManager.playVoice(RES_PET.pet_6);
        } else {
            VoiceManager.playVoice(RES_PET.pet_8);
        }

        const petImage = new cc.Sprite(PET[this._petNumber]);
        petImage.setPosition(POSITION_FOR_PET);
        const ScaleTos = cc.scaleTo(1, 1, 1);
        petImage.setScale(0.0);
        petImage.runAction(ScaleTos);

        this.addChild(petImage, 0, 2);
        this._petNumber += 1;
        if (this._petNumber === 1) {
            this.scheduleOnce(this.petMove, 4);
        } else if (this._petNumber < 3) {
            this.scheduleOnce(this.petMove, 4);
        } else {
            this.scheduleOnce(this.onMoveHand, 8);
        }
    },

    petMove() {
        if (this.getChildByTag(2)) {
            this.removeChildByTag(2);
        }

        const petImage = new cc.Sprite(PET[this._petNumber - 1]);
        let moveTo = '';
        if (this._petNumber === 1) {
            VoiceManager.playVoice(RES_PET.pet_5);
            moveTo = cc.moveTo(1, cc.p(POSITION[0].x
            + 150, POSITION[0].y + 230));
        } else if (this._petNumber === 2) {
            VoiceManager.playVoice(RES_PET.pet_7);
            moveTo = cc.moveTo(1, cc.p(POSITION[1].x
            + 150, POSITION[0].y + 230));
        }
        const ScaleTo = cc.scaleTo(1, 0.8, 0.8);
        petImage.setPosition(POSITION_FOR_PET);
        petImage.runAction(moveTo);
        petImage.runAction(ScaleTo);
        this.addChild(petImage, 0, 3);
        if (this._petNumber < 3) {
            this.scheduleOnce(this.showPet, 6);
        }
    },
    onMoveHand() {
        VoiceManager.playVoice(RES_PET.pet_9);
        this._onpress = true;
        const hand = new cc.Sprite(RES_COMMON.hand);
        hand.setPosition(cc.p(1130, 40));
        hand.setAnchorPoint(cc.p(0, 0));
        const rotateTo1 = cc.rotateTo(0.6, -10.0);
        const rotateTo2 = cc.rotateTo(0.6, 0);
        // const moveUp = cc.MoveTo.create(1, cc.p(1100, 100));
        // const moveDowm = cc.MoveTo.create(1, cc.p(1100, 60));
        const seq = cc.Sequence.create(rotateTo1, rotateTo2);
        const rep = cc.RepeatForever.create(seq);
        this.addChild(hand, 0, 1);
        // hand.runAction(rep);
        hand.runAction(rep);
    },
    onSelect(i) {
        if (!this._onpress) {
            return;
        }
        this._error.hide();
        if (i === 0) {
            this._error.show(cc.p(POSITION[i].x + 370, POSITION[i].y + 50));
            VoiceManager.playVoice(RES_PET.error, () => {
                this._error.hide();
            });
            return;
        }
        if (this.getChildByTag(2)) {
            this.removeChildByTag(2);
            this.removeChildByTag(1);
        }
        this._onpress = false;
        VoiceManager.playVoice(RES_PET.suss);
        const petImage = new cc.Sprite(PET[2]);
        const moveTo = cc.moveTo(1, cc.p(POSITION[1].x
        + 320, POSITION[0].y + 230));
        const ScaleTo = cc.scaleTo(1, 0.8, 0.8);
        petImage.setPosition(POSITION_FOR_PET);
        petImage.runAction(moveTo);
        petImage.runAction(ScaleTo);
        this.addChild(petImage, 0, 3);
        this.guideOver();
    },
    guideOver() {
        const overLayer = new GuideOverLayer(
            this.onGuideAgain.bind(this),
            this.onStart.bind(this),
            EXAM_KEY_PET,
        );
        this.addChild(overLayer, 999);
    },
    onGuideAgain() {
        showPetGuideScene();
    },
    onStart() {
        VoiceManager.stopVoice();
        showPetExamScene();
    },
});

export default function showPetGuideScene() {
    showScene(new PetGuideLayer());
}



