import showScene from '../../common/showScene';
import showCompleteScene from '../base/CompleteScene';
import PainterLayer from './PainterLayer';
import { RoundConfig, RoundDelay, AllDoneDelay } from './round';
import { EXAM_KEY_PAINTER } from '../../constant/exam';
import BGLayer from './BGLayer';

const TAG_PAINTER_LAYER = 1;

const PainterExamLayer = cc.Layer.extend({
    _round: 0,
    _roundInfos: null,
    _examBegin: null,

    ctor() {
        this._super();
        this._roundInfos = [];
        this._examBegin = new Date();
        this.addChild(new BGLayer());
        this.nextRound();
    },

    nextRound() {
        if (this.getChildByTag(TAG_PAINTER_LAYER)) {
            this.removeChildByTag(TAG_PAINTER_LAYER);
        }

        this.addChild(new PainterLayer(RoundConfig[this._round], this.onRoundOver.bind(this)), 0, TAG_PAINTER_LAYER);
    },

    onRoundOver(roundInfo) {
        this._roundInfos.push(roundInfo);
        this._round += 1;

        if (this._round < RoundConfig.length) {
            this.scheduleOnce(this.nextRound, RoundDelay);
        } else {
            this.scheduleOnce(this.onFinish, AllDoneDelay);
        }
    },

    onFinish() {
        showCompleteScene(
            EXAM_KEY_PAINTER,
            { rounds: this._roundInfos, begin: this._examBegin, end: new Date() },
        );
    },
});

export default function showPainterExamScene() {
    showScene(new PainterExamLayer());
}



