import { RES_LOLLIPOP } from '../../resource/lollipop';
// import { RES_APPLE } from '../../resource/apple';

import showIntroScene from '../base/IntroScene';
import showLollipopGuideScene from './LollipopGuideScene';
import BGLayer from './BGLayer';
import { EXAM_KEY_LOLLIPOP } from '../../constant/exam';

export default function () {
    showIntroScene(
        {
            bgLayer: new BGLayer(),
            intro: RES_LOLLIPOP.intro_img,
            btn: RES_LOLLIPOP.btn_img,
            voice: RES_LOLLIPOP.gameName,
        },
        showLollipopGuideScene,
        EXAM_KEY_LOLLIPOP,
    );
}



