import { RES_NUMBER } from '../../resource/number';

const RES_NUMBERS = [
    RES_NUMBER.number_0,
    RES_NUMBER.number_1,
    RES_NUMBER.number_2,
    RES_NUMBER.number_3,
    RES_NUMBER.number_4,
    RES_NUMBER.number_5,
    RES_NUMBER.number_6,
    RES_NUMBER.number_7,
    RES_NUMBER.number_8,
    RES_NUMBER.number_9,
];

const RES_BACKGROUND = [RES_NUMBER.bg_small, RES_NUMBER.bg_small_selected];

const NumberSprite = cc.Sprite.extend({
    _number: -1,

    ctor(number, type, large) {
        this._super(RES_BACKGROUND[type]);

        this._number = number;

        const rect = this.getTextureRect();
        const numberSprite = new cc.Sprite(RES_NUMBERS[number]);
        numberSprite.setPosition(rect.width / 2, rect.height / 2);

        if (type === NumberSprite.SmallSelected) {
            numberSprite.setOpacity(127);
        }

        this.addChild(numberSprite);

        if (large) {
            this.setScale(1.5);
        }
    },

    getNumber() {
        return this._number;
    },
});

NumberSprite.Small = 0;
NumberSprite.SmallSelected = 1;

export default NumberSprite;



