import { POSITION_FOR_ONE, PartDuration, PartInterval } from './round';
import { RES_PAINTER } from '../../resource/painter';
import TimeoutLayer from '../../common/TimeoutLayer';
import { EXAM_KEY_PAINTER } from '../../constant/exam';

const TAG_TIPS_LAYER = 1;
const TAG_MENU = 10;
const PainterLayer = cc.Layer.extend({
    _config: null,
    _cbSelect: null,
    _begin: null,
    _isRepeat: false,

    ctor(config, cbSelect) {
        this._super();
        this._config = config;
        this._cbSelect = cbSelect;
        const { color, shape } = config;
        this._tips = [RES_PAINTER.paint[color], RES_PAINTER[shape].normal];
        this._step = 0;

        this.showTips();
    },
    showTips() {
        const layer = new cc.Layer();
        const sprite = new cc.Sprite(this._tips[this._step]);
        sprite.setPosition(POSITION_FOR_ONE);
        sprite.attr({ scale: 0.75 });
        layer.addChild(sprite);
        this.addChild(layer, 0, TAG_TIPS_LAYER);
        this.scheduleOnce(this.clearTips, PartDuration);
    },
    showSelect() {
        const menu = new cc.Menu();
        menu.setPosition(cc.p(0, 0));
        const { position, select } = this._config;
        position.map((pos, index) => {
            const { color, shape } = select[index];
            const menuItem = new cc.MenuItemImage(RES_PAINTER[shape][color], null, this.onSelect.bind(this, index));
            menuItem.setPosition(pos);
            menuItem.attr({ scale: 0.75 });
            menu.addChild(menuItem);
            return pos;
        });
        this.addChild(menu, 0, TAG_MENU);
        this._begin = new Date();
        this.scheduleOnce(this.onSelectTimeout, this._config.roundTimeout);
    },
    onSelect(index, menuItem) {
        if (this._isRepeat) return;
        this._isRepeat = true;
        menuItem.attr({ opacity: 128 });
        const result = {
            success: this._config.target === this._config.select[index],
            reaction: new Date().getTime() - this._begin.getTime(),
            timeout: false,
            count: this._config.position.length,
            selectedColor: this._config.select[index].color,
            selectedShape: this._config.select[index].shape,
        };
        if (this._cbSelect) {
            this._cbSelect(result);
        }
    },
    clearTips() {
        if (this.getChildByTag(TAG_TIPS_LAYER)) {
            this.removeChildByTag(TAG_TIPS_LAYER);
        }
        this._step += 1;

        if (this._step === this._tips.length) {
            this.scheduleOnce(this.showSelect, PartInterval);
        } else {
            this.scheduleOnce(this.showTips, PartInterval);
        }
    },
    onSelectTimeout() {
        if (this._isRepeat) return;
        this._isRepeat = true;
        this.addChild(new TimeoutLayer(EXAM_KEY_PAINTER));
        if (this._cbSelect) {
            this._cbSelect({
                success: false,
                reaction: this._config.roundTimeout * 1000,
                timeout: true,
                count: this._config.position.length,
                selectedColor: null,
                selectedShape: null,
            });
        }
    },
});

export default PainterLayer;



