import calcPainter from '../exam/painter';
import calcPet from '../exam/pet';
import { EXAM_KEY_PAINTER, EXAM_KEY_PET } from '../../../constant/exam';
import updateResult from './updateResult';

export default function calcSolution(result) {
    const solutionResult = {
        totalTime: 0,
        result: { score: 0 },
    };

    updateResult(solutionResult, result, calcPainter, EXAM_KEY_PAINTER);
    updateResult(solutionResult, result, calcPet, EXAM_KEY_PET);

    return solutionResult;
}



