import { RES_APPLE } from '../../resource/apple';
import showIntroScene from '../base/IntroScene';
import showAppleGuideScene from './AppleGuideScene';
import BGLayer from './BGLayer';
import { EXAM_KEY_APPLE } from '../../constant/exam';

export default function () {
    showIntroScene(
        {
            bgLayer: new BGLayer(),
            intro: RES_APPLE.intro_img,
            btn: RES_APPLE.btn_img,
            voice: RES_APPLE.intro,
        },
        showAppleGuideScene,
        EXAM_KEY_APPLE,
    );
}



