import showScene from '../../common/showScene';
import showCompleteScene from '../base/CompleteScene';
import { EXAM_KEY_CONNECTION } from '../../constant/exam';
import ConnectionLayer from './ConnectionLayer';
import { RoundDelay, RoundConfig, AllDoneDelay } from './round';
import BGLayer from './BGLayer';

const TAG_CONNECTION_LAYER = 1;

const ConnectionExamLayer = cc.Layer.extend({
    _round: 0,
    _examBegin: null,
    _roundInfos: null,

    ctor() {
        this._super();

        this._roundInfos = [];
        this._examBegin = new Date();

        this.addChild(new BGLayer());

        this.nextRound();
    },

    nextRound() {
        if (this.getChildByTag(TAG_CONNECTION_LAYER)) {
            this.removeChildByTag(TAG_CONNECTION_LAYER);
        }

        this.addChild(
            new ConnectionLayer(RoundConfig[this._round], this.onRoundOver.bind(this)),
            0,
            TAG_CONNECTION_LAYER,
        );
    },

    onRoundOver(roundInfo) {
        this._roundInfos.push(roundInfo);

        this._round += 1;

        if (this._round < RoundConfig.length) {
            this.scheduleOnce(this.nextRound, RoundDelay);
        } else {
            this.scheduleOnce(this.allDone, AllDoneDelay);
        }
    },

    allDone() {
        showCompleteScene(
            EXAM_KEY_CONNECTION,
            { rounds: this._roundInfos, begin: this._examBegin, end: new Date() },
        );
    },
});

export default function showConnectionExamScene() {
    showScene(new ConnectionExamLayer());
}



