import { RES_PAINTER } from '../../resource/painter';
import showIntroScene from '../base/IntroScene';
import showPainterGuideScene from './PainterGuideScene';
import BGLayer from './BGLayer';
import { EXAM_KEY_PAINTER } from '../../constant/exam';

export default function () {
    showIntroScene(
        {
            bgLayer: new BGLayer(),
            intro: RES_PAINTER.other.intro_img,
            btn: RES_PAINTER.other.btn_img,
            voice: RES_PAINTER.voice.intro,
        },
        showPainterGuideScene,
        EXAM_KEY_PAINTER,
    );
}



