import { RES_CANDY } from '../../resource/candy';
import { generatePosition } from './position';
import { RoundTimeout } from './round';
import TimeoutLayer from '../../common/TimeoutLayer';
import { EXAM_KEY_CANDY } from '../../constant/exam';

const CORRECT_TAG = 0;

const CandyLayer = cc.Layer.extend({
    _cbSelect: null,
    _begin: null,
    _roundOver: false,

    ctor(candyCount, cbSelect) {
        this._super();

        this._roundOver = false;
        this._cbSelect = cbSelect;

        const positions = generatePosition(candyCount);
        const menu = new cc.Menu();
        menu.setPosition(0, 0);

        const yellowMenuItem = new cc.MenuItemImage(RES_CANDY.candy_yellow, null, this.onCandyPressed, this);
        yellowMenuItem.setPosition(positions[0]);
        yellowMenuItem.setAnchorPoint(cc.p(0, 0));
        menu.addChild(yellowMenuItem, 0, 0);

        for (let i = 1; i < candyCount; i += 1) {
            const redMenuItem = new cc.MenuItemImage(RES_CANDY.candy_red, null, this.onCandyPressed, this);
            redMenuItem.setPosition(positions[i]);
            redMenuItem.setAnchorPoint(cc.p(0, 0));
            menu.addChild(redMenuItem, 0, i);
        }

        this.addChild(menu);
    },

    onEnter() {
        this._super();
        this._begin = new Date();
        this.scheduleOnce(this.onSelectTimeout, RoundTimeout);
    },

    onCandyPressed(menuItem) {
        if (this._roundOver) {
            return;
        }

        const tag = menuItem.getTag();
        const result = {
            success: tag === CORRECT_TAG,
            reaction: new Date().getTime() - this._begin.getTime(),
            timeout: false,
        };

        if (this._cbSelect) {
            this._roundOver = true;
            this._cbSelect(result);
        }

        let textureSelected = null;

        if (tag === CORRECT_TAG) {
            textureSelected = cc.textureCache.addImage(RES_CANDY.candy_yellow_selected);
        } else {
            textureSelected = cc.textureCache.addImage(RES_CANDY.candy_red_selected);
        }

        const selectRect = new cc.Rect(0, 0, textureSelected.width, textureSelected.height);
        const selectFrame = new cc.SpriteFrame(textureSelected, selectRect);

        menuItem.getNormalImage().setSpriteFrame(selectFrame);
    },

    onSelectTimeout() {
        this._roundOver = true;

        if (this._cbSelect) {
            this._cbSelect({
                success: false,
                reaction: RoundTimeout * 1000,
                timeout: true,
            });
        }

        this.addChild(new TimeoutLayer(EXAM_KEY_CANDY));
    },
});

export default CandyLayer;



