

import { RES_COMMON } from '../../resource/common';

const TAG_HAND = 123;
const HandleUtil = cc.Layer.extend({
    _isShow: false,
    _isBig: true,
    _menu: null,
    _hand: null,
    ctor(menu, isBig, cb) {
        this._super();
        this._isBig = isBig;
        this._isShow = false;
        this._menu = menu;
        this._hand = new cc.MenuItemImage(
            this._isBig ? RES_COMMON.big_hand : RES_COMMON.small_hand,
            null,
            cb && null,
            this,
        );
    },

    show(position, cb) {
        if (this._isShow) return;
        this._isShow = true;
        if (cb) {
            this._hand.setCallback(cb);
        }
        if (position) {
            this._hand.setPosition(position);
        }
        const rotateTo1 = cc.rotateTo(0.6, -10.0);
        const rotateTo2 = cc.rotateTo(0.6, 0);
        const seq = cc.Sequence.create(rotateTo1, rotateTo2);
        const rep = cc.RepeatForever.create(seq);
        this._hand.runAction(rep);
        this._menu.addChild(this._hand, 0, TAG_HAND);
    },
    setPosition(position) {
        this._hand.setPosition(position);
    },
    hide() {
        if (!this._isShow) return;
        this._isShow = false;
        this._menu.removeChildByTag(TAG_HAND);
    },
    move(time, position) {
        if (!this._isShow) return;
        const moveTo = cc.moveTo(time, position);
        const hand = this._menu.getChildByTag(TAG_HAND);
        hand.runAction(moveTo);
    },
});

export default HandleUtil;



