import { EXAM_KEY_CANDY } from '../../constant/exam';
import CandyLayer from './CandyLayer';
import BGLayer from './BGLayer';
import showScene from '../../common/showScene';
import showCompleteScene from '../base/CompleteScene';
import { RoundCount, RoundDelay, RoundTimeoutDelay, AllDoneDelay } from './round';

const TAG_CANDY_LAYER = 1;

const CandyExamLayer = cc.Layer.extend({
    _roundInfos: null,
    _round: 0,
    _examBegin: null,

    ctor() {
        this._super();
        this._roundInfos = [];
        this._round = 0;

        this.addChild(new BGLayer());

        this._examBegin = new Date();

        this.nextRound();
    },

    nextRound() {
        if (this.getChildByTag(TAG_CANDY_LAYER)) {
            this.removeChildByTag(TAG_CANDY_LAYER);
        }

        this.scheduleOnce(this.showCandyLayer, RoundDelay + Math.random());
    },

    showCandyLayer() {
        const candyLayer = new CandyLayer(RoundCount[this._round], this.onRoundOver.bind(this));
        this.addChild(candyLayer, 0, TAG_CANDY_LAYER);
    },

    onRoundOver(roundInfo) {
        this._roundInfos.push(roundInfo);

        this._round += 1;

        if (this._round < RoundCount.length) {
            this.scheduleOnce(this.nextRound, roundInfo.timeout ? RoundTimeoutDelay : RoundDelay);
        } else {
            this.scheduleOnce(this.allDone, AllDoneDelay);
        }
    },

    allDone() {
        showCompleteScene(EXAM_KEY_CANDY, { rounds: this._roundInfos, begin: this._examBegin, end: new Date() });
    },
});

export default function showCandyExamScene() {
    showScene(new CandyExamLayer());
}



