export function generateRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
}

export function generateRandomDiffInt(min, max, diffInt) {
    let randomDiffInt = generateRandomInt(min, max);
    while (randomDiffInt === diffInt) {
        randomDiffInt = generateRandomInt(min, max);
    }

    return randomDiffInt;
}



