import showScene from '../../common/showScene';
import examManager from '../';
import VoiceManager from '../../common/voiceManager';
import HeaderLayer from '../base/HeaderLayer';
import { RES_MAIN } from '../../resource/main';
import { goBack } from '../../common/invoke';
import { EXAM_KEY_MAIN } from '../../constant/exam';

const ExamIntroLayer = cc.Layer.extend({
    ctor() {
        this._super();

        const bgLayer = new cc.LayerColor(cc.color(244, 255, 255));
        this.addChild(bgLayer);

        const { width, height } = cc.director.getWinSize();

        const title = new cc.Sprite(RES_MAIN.title);
        title.setPosition(width / 2, height - 230);
        this.addChild(title);

        this.header = new HeaderLayer(
            EXAM_KEY_MAIN,
            goBack,
            false,
            this.playIntro.bind(this),
            null,
            {
                backImage: RES_MAIN.btn_exit,
                unmuteImage: RES_MAIN.btn_unmute,
                muteImage: RES_MAIN.btn_mute,
                repeatImage: RES_MAIN.btn_repeat,
            },
        );
        this.addChild(this.header, 99);

        const menu = new cc.Menu();
        menu.setPosition(0, 0);
        this.addChild(menu);

        const btnStart = new cc.MenuItemImage(RES_MAIN.btn_start, null, this.onStartPressed, this);
        btnStart.setPosition(width / 2, 230);
        menu.addChild(btnStart);

        const tips = new cc.LabelTTF('注：本次评估所采集的数据，将仅用于评估孩子的认知能力分数', '', 28);
        tips.setPosition(width / 2, 30);
        tips.setColor(cc.color(135, 135, 135));
        this.addChild(tips);
        this.playIntro();
    },

    playIntro() {
        VoiceManager.playVoice(RES_MAIN.intro, this.setRepeat.bind(this));
    },

    setRepeat() {
        this.header.setRepeat();
    },

    onStartPressed() {
        VoiceManager.stopVoice();
        examManager.next();
    },
});

export default function showMainScene() {
    showScene(new ExamIntroLayer());
}



