import { RES_HEADER_LAYER } from '../../resource/headerLayer';
import VoiceManager from '../../common/voiceManager';
import ExitGuide from './ExitGuide';
import ExitGame from './ExitGame';
import { invoke, getMute } from '../../common/invoke';

const HeaderLayer = cc.Layer.extend({
    _isMute: null,
    _btnMute: null,
    _cbExit: null,
    _cbMute: null,
    _repeat: null,
    _gogo: null,
    _backImage: null,
    _muteImage: null,
    _unmuteImage: null,
    _repeatImage: null,
    _enableRepeat: null,
    _examKey: null,

    ctor(examKey, cbExit, cbMute, repeat, gogo, {
        backImage, muteImage, unmuteImage, repeatImage,
    } = {}) {
        this._super();
        this._cbExit = cbExit;
        this._cbMute = cbMute;
        this._isMute = false;
        this._repeat = repeat;
        this._gogo = gogo;
        this._enableRepeat = false;
        this._backImage = backImage || RES_HEADER_LAYER.btn_back;
        this._muteImage = muteImage || RES_HEADER_LAYER.btn_mute;
        this._unmuteImage = unmuteImage || RES_HEADER_LAYER.btn_unmute;
        this._repeatImage = repeatImage;
        this._volume = null;
        this._getPlayState = false;
        this._examKey = examKey;
        const { width, height } = cc.director.getWinSize();
        const menu = new cc.Menu();
        menu.setPosition(0, 0);
        this.addChild(menu);


        if (this._backImage) {
            const backTexture = cc.textureCache.addImage(this._backImage);

            const btnExit = new cc.MenuItemImage(this._backImage, null, this.onExitPressed, this);
            btnExit.setPosition((backTexture.width / 2) + 20, height - 52);
            menu.addChild(btnExit);
        }
        this._btnMute = new cc.MenuItemImage(this._unmuteImage, null, this.onAudioPressed, this);
        this._btnMute.setPosition(width - 56, height - 52);
        menu.addChild(this._btnMute);
        this.initVolume();
    },

    initVolume() {
        getMute().then((data) => {
            if (data === 0) {
                this.changeIcon(this._muteImage);
            } else {
                this.changeIcon(this._unmuteImage);
            }
        }).then(() => {
            invoke
                .define('listenMute', this.listenMute.bind(this))
                .define('listenAppState', this.listenAppState.bind(this));
        });
    },

    listenAppState(appState) {
        if (VoiceManager.getPlayState()) {
            this._getPlayState = true;
            if (appState !== 'active') {
                VoiceManager.pauseMusic(appState);
            }
        }
        if (appState === 'active' && this._getPlayState) {
            this._getPlayState = false;
            VoiceManager.resumeMusic(appState);
        }
    },

    listenMute(volume) {
        this._volume = volume;
        if (volume === 0 && !this._enableRepeat) {
            this._isMute = true;
            this.changeIcon(this._muteImage);
        } else if (volume > 0 && !this._enableRepeat) {
            this.changeIcon(this._unmuteImage);
            this._isMute = false;
        }
    },

    onExitPressed() {
        if (this._gogo) {
            this.addChild(new ExitGuide(this._cbExit, this._gogo, this._examKey));
        } else {
            this.addChild(new ExitGame(this._cbExit, this.removeExitGame.bind(this), this._examKey), 3, 123);
        }


        if (this._cbExit) {
            // this._cbExit();
        }
    },

    removeExitGame() {
        if (this.getChildByTag(123)) {
            this.removeChildByTag(123);
        }
    },

    onAudioPressed() {
        if (this._cbMute) {
            this._cbMute();
        }

        if (this._repeat && this._enableRepeat) {
            this._repeat();
            this._enableRepeat = false;
            if (this._isMute) {
                this._isMute = false;
                VoiceManager.cancelMute();
            }

            this.changeIcon(this._unmuteImage);
        } else {
            this.toggleMute();
        }
    },

    setRepeat() {
        if (this._repeatImage) {
            this.changeIcon(this._repeatImage);
            this.changeMuteBtnPosition(-104, -52);
            this._enableRepeat = true;
        }
    },
    toggleMute() {
        this._isMute = !this._isMute;

        let res = this._unmuteImage;

        if (this._isMute) {
            VoiceManager.setMute();
            res = this._muteImage;
        } else {
            VoiceManager.cancelMute();
        }
        this.changeIcon(res);
    },

    changeMuteBtnPosition(offsetWidth, offsetHeight) {
        const { width, height } = cc.director.getWinSize();
        this._btnMute.setPosition(width + offsetWidth, height + offsetHeight);
    },

    changeIcon(res) {
        const texture = cc.textureCache.addImage(res);
        const rect = new cc.Rect(0, 0, texture.width, texture.height);
        const spriteFrame = new cc.SpriteFrame(texture, rect);
        this._btnMute.getNormalImage().setSpriteFrame(spriteFrame);
        this._btnMute._setWidth(texture.width);
        this.changeMuteBtnPosition(-56, -52);
    },
});

export default HeaderLayer;



