import showScene from '../../common/showScene';
import showCompleteScene from '../base/CompleteScene';
import { EXAM_KEY_PET } from '../../constant/exam';
import PetLayer from './PetLayer';
import { RoundConfig, RoundDelay, AllDoneDelay } from './round';
import BGLayer from './BGLayer';

const TAG_PET_LAYER = 1;

const PetExamLayer = cc.Layer.extend({
    _round: 0,
    _roundInfos: null,
    _examBegin: null,
    ctor() {
        this._super();
        this._roundInfos = [];
        this._round = 0;
        this._examBegin = new Date();
        const bgLayer = new BGLayer();
        this.addChild(bgLayer);

        this.nextRound();
    },

    nextRound() {
        if (this.getChildByTag(TAG_PET_LAYER)) {
            this.removeChildByTag(TAG_PET_LAYER);
        }
        this.addChild(new PetLayer(RoundConfig[this._round], this.onRoundOver.bind(this)), 0, TAG_PET_LAYER);
    },


    onRoundOver(roundInfos) {
        this._roundInfos.push(roundInfos);
        this._round += 1;
        if (this._round < RoundConfig.length) {
            this.scheduleOnce(this.nextRound, RoundDelay);
        } else {
            this.scheduleOnce(this.onFinish, AllDoneDelay);
        }
    },
    onFinish() {
        showCompleteScene(
            EXAM_KEY_PET,
            { rounds: this._roundInfos, begin: this._examBegin, end: new Date() },
        );
    },

});

export default function showPetExamScene() {
    showScene(new PetExamLayer());
}



