import examManager from '../';
import showScene from '../../common/showScene';
import { RES_COMMON } from '../../resource/common';
import VoiceManager from '../../common/voiceManager';
import { EXAM_BG_COLORS_BY_KEY } from '../../constant/common';

const CompleteLayer = cc.Layer.extend({
    _cbNext: null,

    ctor(bgColor) {
        this._super();
        const winSize = cc.director.getWinSize();
        const bgLayer = new cc.LayerColor(bgColor);
        const hasNext = examManager.hasNext();
        const res = hasNext ? RES_COMMON.next_game : RES_COMMON.last_game;
        const sprite = new cc.Sprite(res);
        sprite.setPosition(winSize.width / 2, (winSize.height / 2) + 36);

        if (!hasNext) {
            let px = 614;
            const py = 47;
            const dx = 15;
            [1, 2, 3].forEach((x, index) => {
                const dot = new cc.Sprite(RES_COMMON.last_game_dot);
                dot.setPosition(cc.p(px += dx, py));
                const action = cc.sequence(
                    cc.fadeOut(0),
                    cc.delayTime((index + 1) * 0.6),
                    cc.fadeIn(0),
                    cc.delayTime((3 - index) * 0.6),
                );
                sprite.addChild(dot);
                dot.runAction(action.repeatForever());
            });
        }

        VoiceManager.playVoice(!hasNext ? RES_COMMON.round_encourage : RES_COMMON.exam_encourage);
        if (hasNext) {
            this.scheduleOnce(this.toNextExam.bind(this), 5);
        } else {
            this.scheduleOnce(this.toNextExam.bind(this), 6);
        }
        this.addChild(bgLayer);
        this.addChild(sprite);
    },

    toNextExam() {
        examManager.next();
    },
});

export default function showCompleteScene(key, result) {
    examManager.reportResult(key, result);
    showScene(new CompleteLayer(EXAM_BG_COLORS_BY_KEY[key]));
}



