import {
    EXAM_KEY_LOLLIPOP,
    LEVEL1,
    LEVEL2,
    LEVEL3,
    INVALID_REACTION,
} from '../../../constant/exam';
import configManager from '../../../config';


function calcLollipopLevelScore(round1, round2, level) {
    const levelConfig = configManager.getLevelConfig(EXAM_KEY_LOLLIPOP, level.index);

    const result = { items: [round1, round2], level: level.value };

    let correctCount = 0;
    let accuracyTotal = 0;
    let reactionTotal = 0;

    for (let i = 0; i < result.items.length; i += 1) {
        const {
            dishCount,
            correct,
            selected,
            reaction,
        } = result.items[i];
        const correctSet = new Set(correct);
        const selectedSet = new Set(selected);
        let roundCorrectCount = 0;

        for (let j = 0; j < dishCount; j += 1) {
            if (correctSet.has(j) === selectedSet.has(j)) {
                roundCorrectCount += 1;
            }
        }

        if (roundCorrectCount > 0) {
            accuracyTotal += roundCorrectCount / dishCount;
            reactionTotal += reaction;
            correctCount += 1;
        }
    }

    if (correctCount === 0) {
        result.average_reaction = INVALID_REACTION;
        result.accuracy = 0;
        result.score = 0;
        result.original_score = 0;

        return result;
    }


    const averageReaction = reactionTotal / correctCount;
    const averageAccuracy = accuracyTotal / result.items.length;

    let score = averageAccuracy * 100;

    if (averageReaction <= levelConfig.reaction_threshold) {
        score += 10;
    }

    result.original_score = score;
    result.score = score;
    if (score > levelConfig.max_score) {
        result.score = levelConfig.max_score;
    }
    if (score < 0) {
        result.score = 0;
    }
    result.accuracy = averageAccuracy;
    result.average_reaction = averageReaction;

    return result;
}

export default function calcLollipop(result) {
    const { begin, end, rounds } = result[EXAM_KEY_LOLLIPOP];

    const level1 = calcLollipopLevelScore(rounds[0], rounds[1], LEVEL1);
    const level2 = calcLollipopLevelScore(rounds[2], rounds[3], LEVEL2);
    const level3 = calcLollipopLevelScore(rounds[4], rounds[5], LEVEL3);

    const score = (level1.score * 0.2) + (level2.score * 0.3) + (level3.score * 0.5);

    return {
        score,
        begin,
        end,
        levels: [level1, level2, level3],
    };
}



