import ScoreScene from './ScoreScene';
import calculator from './calculator';
import { submitResult, setBgColor } from '../common/invoke';
import { getScore, getAssess } from './const';

class ScoreManager {
    showScore(result, begin, end, type = '') {
        const data = calculator(result, begin, end);

        const fromApp = JSON.parse(window.sessionStorage.getItem('fromApp'));
        if (fromApp && fromApp.isFromApp) {
            const examData = getScore(data.exam_data);
            const assessData = getAssess(examData);

            window.parent.postMessage(
                { type: 'show_report', assessData: assessData[type] },
                '*',
            );
        } else {
            cc.director.runScene(new ScoreScene(getScore(data.exam_data)));

            setBgColor('#F9FFFF');

            submitResult([data])
                .then((res) => {
                    const score = res.payload.result.scores[0];
                    if (score) {
                        cc.director.runScene(new ScoreScene(score));
                    }
                })
                .catch(() => {});
        }
    }
}

export default new ScoreManager();



