import calcConnection from '../exam/connection';
import calcShape from '../exam/shape';
import { EXAM_KEY_CONNECTION, EXAM_KEY_SHAPE } from '../../../constant/exam';
import updateResult from './updateResult';

export default function calcExecutive(result) {
    const executiveResult = {
        totalTime: 0,
        result: { score: 0 },
    };

    updateResult(executiveResult, result, calcConnection, EXAM_KEY_CONNECTION);
    updateResult(executiveResult, result, calcShape, EXAM_KEY_SHAPE);

    return executiveResult;
}



