import showScene from '../../common/showScene';
import showCompleteScene from '../base/CompleteScene';
import ShapeLayer from './ShapeLayer';
import { EXAM_KEY_SHAPE } from '../../constant/exam';
import { RoundSameShape, RoundDelay, AllDoneDelay } from './round';
import { ROPE_ARRAY, SHAPE_ARRAY, BIG_ROPE, SMALL_ROPE, POSITIONS, BIG_ROPE_CENTRE } from './position';
import { generateRandomInt, generateRandomDiffInt } from '../../common/RandomInt';
import BGLayer from './BGLayer';

const TAG_SHAPE_LAYER = 1;

const ShapeExamLayer = cc.Layer.extend({
    _currentRoundDetail: null,
    _roundInfos: null,
    _sameShape: null,
    _isBigRope: null,
    _round: 0,
    _examBegin: null,
    _firstShape: null,

    ctor() {
        this._super();

        this._currentRoundDetail = {};
        this._roundInfos = [];
        this._sameShape = [];
        this._isBigRope = [];
        this._examBegin = new Date();

        const bgLayer = new BGLayer();
        this.addChild(bgLayer);

        this.nextRound();
    },

    nextRound() {
        if (this.getChildByTag(TAG_SHAPE_LAYER)) {
            this.removeChildByTag(TAG_SHAPE_LAYER);
        }

        this.scheduleOnce(this.showShapeLayer, 0.5 + Math.random());
    },

    showShapeLayer() {
        const {
            bigShape,
            bigShapePositions,
            smallShape,
            ropeType,
            ropePosition,
            disturberPosition,
        } = this.getShapeAndRope(this._round);

        this._currentRoundDetail = {
            bigShape, smallShape, ropeType, ropePosition, disturberPosition,
        };

        const appleLayer = new ShapeLayer({
            bigShape,
            bigShapePositions,
            smallShape,
            ropeType,
            ropePosition,
            disturberPosition,
            cbSelect: this.onRoundOver.bind(this),
        });

        this.addChild(appleLayer, 0, TAG_SHAPE_LAYER);
    },

    getShapeAndRope(round) {
        const bigShape = round === 1 && this._firstShape ?
            generateRandomDiffInt(0, SHAPE_ARRAY.length, this._firstShape) : generateRandomInt(0, SHAPE_ARRAY.length);
        let smallShape = generateRandomInt(0, SHAPE_ARRAY.length);
        let ropeType;
        const bigShapePositions = POSITIONS[bigShape];
        let ropePositionIndex = generateRandomInt(0, bigShapePositions.length);
        let ropePosition = bigShapePositions[ropePositionIndex];
        let disturberPosition = null;

        if (round % 2 !== 0) {
            const isPrevSameShape = this._sameShape[round - 1];
            const isPrevBigRope = this._isBigRope[round - 1];
            if (isPrevSameShape) {
                smallShape = generateRandomDiffInt(0, SHAPE_ARRAY.length, bigShape);
            } else {
                smallShape = bigShape;
            }
            ropeType = isPrevBigRope ? SMALL_ROPE : BIG_ROPE;
        } else {
            ropeType = generateRandomInt(0, ROPE_ARRAY.length);
        }

        if (ropeType === BIG_ROPE) {
            ropePosition = BIG_ROPE_CENTRE;
            ropePositionIndex = false;
            smallShape = generateRandomDiffInt(0, SHAPE_ARRAY.length, bigShape);
        } else if (round === 2) {
            smallShape = bigShape;
        }

        if (RoundSameShape[round]) {
            smallShape = bigShape;
        }

        if (round > 3) {
            smallShape = generateRandomDiffInt(0, SHAPE_ARRAY.length, bigShape);
            disturberPosition = ropePositionIndex ?
                generateRandomDiffInt(0, bigShapePositions.length, ropePositionIndex) :
                generateRandomInt(0, bigShapePositions.length);
        } else if (round === 0) {
            this._firstShape = bigShape;
        }

        const isSameShape = bigShape === smallShape;
        this._sameShape.push(isSameShape);
        const isBigRope = ropeType === BIG_ROPE;
        this._isBigRope.push(isBigRope);

        return {
            bigShape,
            bigShapePositions,
            smallShape,
            ropeType,
            ropePosition,
            disturberPosition,
        };
    },

    onRoundOver(roundInfo) {
        this._roundInfos.push({ ...roundInfo, ...this._currentRoundDetail });

        this._round += 1;

        if (this._round < RoundSameShape.length) {
            this.scheduleOnce(this.nextRound, RoundDelay);
        } else {
            this.scheduleOnce(this.onFinish, AllDoneDelay);
        }
    },

    onFinish() {
        showCompleteScene(
            EXAM_KEY_SHAPE,
            { rounds: this._roundInfos, begin: this._examBegin, end: new Date() },
        );
    },
});

export default function () {
    showScene(new ShapeExamLayer());
}



