import showScene from '../../common/showScene';
import showPainterExamScene from './PainterExamScene';
import GuideOverLayer from '../../common/GuideOverLayer';
import { EXAM_KEY_PAINTER } from '../../constant/exam';
import HeaderLayer from '../base/HeaderLayer';
import GuideFirstPage from '../base/GuideFirstPage';
import VoiceManager from '../../common/voiceManager';
import HandleUtil from '../base/HandleUtil';
import ErrorTips from '../base/ErrorTips';
import { RES_PAINTER } from '../../resource/painter';
import { guideConfig, POSITION_FOR_ONE, PartDuration } from './round';
import BGLayer from './BGLayer';

const BG_LAYER = 0;
const TAG_TIPS_LAYER = 1;
const PainterGuideLayer = cc.Layer.extend({
    _error: null,
    _hand: null,

    ctor() {
        this._super();
        this._isReady = false;
        this.addChild(new BGLayer());
        this.addChild(new GuideFirstPage(EXAM_KEY_PAINTER, 13, new BGLayer()), 0, BG_LAYER);
        const header = new HeaderLayer(
            EXAM_KEY_PAINTER,
            this.startExam.bind(this),
            null,
            null,
            this.onGuideAgain.bind(this),
        );
        this.addChild(header, 99);
        VoiceManager.playVoice(
            RES_PAINTER.voice.guide,
            () => { this.scheduleOnce(this.showColorTip.bind(this), PartDuration); },
        );
    },

    showColorTip() {
        if (this.getChildByTag(BG_LAYER)) {
            this.removeChildByTag(BG_LAYER);
        }
        const { color } = guideConfig;
        const layer = new cc.Layer();
        const sprite = new cc.Sprite(RES_PAINTER.paint[color]);
        sprite.setPosition(POSITION_FOR_ONE);
        sprite.attr({ scale: 0.75 });
        layer.addChild(sprite);
        this.addChild(layer, 0, TAG_TIPS_LAYER);
        VoiceManager.playVoice(
            RES_PAINTER.voice.tip_color,
            () => { this.scheduleOnce(this.showShapeTip.bind(this), PartDuration); },
        );
    },

    showShapeTip() {
        if (this.getChildByTag(TAG_TIPS_LAYER)) {
            this.removeChildByTag(TAG_TIPS_LAYER);
        }
        const { shape } = guideConfig;
        const layer = new cc.Layer();
        const sprite = new cc.Sprite(RES_PAINTER[shape].normal);
        sprite.setPosition(POSITION_FOR_ONE);
        sprite.attr({ scale: 0.75 });
        layer.addChild(sprite);
        this.addChild(layer, 0, TAG_TIPS_LAYER);
        VoiceManager.playVoice(
            RES_PAINTER.voice.tip_shape,
            () => { this.scheduleOnce(this.showSelect.bind(this), PartDuration); },
        );
    },

    showSelect() {
        if (this.getChildByTag(TAG_TIPS_LAYER)) {
            this.removeChildByTag(TAG_TIPS_LAYER);
        }
        const menu = new cc.Menu();
        menu.setPosition(cc.p(0, 0));
        this._hand = new HandleUtil(menu, false);
        this._error = new ErrorTips(menu, false);
        const { position, select, target } = guideConfig;
        position.map((pos, index) => {
            const { color, shape } = select[index];
            const onSelect = (target.color === color && target.shape === shape)
                ? (this._hand.setPosition(cc.p({ x: pos.x + 60, y: pos.y - 100 })), this.onRightSelect.bind(this))
                : this.onErrorSelect.bind(this);
            const menuItem = new cc.MenuItemImage(RES_PAINTER[shape][color], null, onSelect);
            menuItem.setPosition(pos);
            menuItem.attr({ scale: 0.75 });
            menu.addChild(menuItem, -1, index);
            return pos;
        });
        this.addChild(menu);
        VoiceManager.playVoice(RES_PAINTER.voice.tip, () => {
            this._isReady = true;
            this._hand.show();
        });
    },

    onRightSelect(menuItem) {
        if (!this._isReady) return;
        this._error.hide();
        this._isReady = false;
        menuItem.attr({ opacity: 128 });
        VoiceManager.playVoice(RES_PAINTER.voice.right);
        this._hand.hide();
        const overLayer = new GuideOverLayer(
            this.onGuideAgain.bind(this),
            this.startExam.bind(this),
            EXAM_KEY_PAINTER,
        );
        this.addChild(overLayer, 999);
    },

    onGuideAgain() {
        VoiceManager.stopVoice();
        showPainterGuideScene();
    },

    onErrorSelect(menuItem) {
        if (!this._isReady) return;
        this._error.hide();
        menuItem.attr({ opacity: 128 });
        menuItem.scheduleOnce(() => { menuItem.attr({ opacity: 255 }); }, 0.5);
        this._isReady = false;
        this._error.show(cc.p(menuItem.x + 96, menuItem.y - 88));
        VoiceManager.playVoice(RES_PAINTER.voice.error, () => {
            this._isReady = true;
            this._error.hide();
        });
    },

    startExam() {
        VoiceManager.stopVoice();
        showPainterExamScene();
    },
});

export default function showPainterGuideScene() {
    showScene(new PainterGuideLayer());
}



