import { setMute, cancelMute } from './invoke';

class VoiceManager {
    constructor() {
        this._mute = false;
        this._cbDone = null;
        this._currentMusic = null;
    }

    playVoice(music, cbDone) {
        this._currentMusic = music;
        this._cbDone = cbDone;
        cc.audioEngine.playMusic(music);

        if (this._timer) {
            clearInterval(this._timer);
        }

        this._timer = setInterval(this._checkDone.bind(this), 500);
    }

    _checkDone() {
        if (this._cbDone && this._currentMusic && !cc.audioEngine.isMusicPlaying()) {
            clearInterval(this._timer);
            this._cbDone();
        }
    }
    pauseMusic() {
        cc.audioEngine.pauseMusic();
        if (this._timer) {
            clearInterval(this._timer);
            this._timer = null;
        }
    }
    resumeMusic() {
        cc.audioEngine.resumeMusic();
        this._timer = setInterval(this._checkDone.bind(this), 500);
    }
    stopVoice() {
        this._currentMusic = null;
        this._cbDone = null;
        this._paused = false;

        if (this._timer) {
            clearInterval(this._timer);
            this._timer = null;
        }

        cc.audioEngine.stopMusic();
    }

    setMute() {
        if (cc.audioEngine.isMusicPlaying()) {
            cc.audioEngine.setMusicVolume(0);
            setMute();
        }
    }

    cancelMute() {
        if (cc.audioEngine.isMusicPlaying()) {
            cc.audioEngine.setMusicVolume(1);
            cancelMute();
        }
    }

    getPlayState() {
        return cc.audioEngine.isMusicPlaying();
    }
}

export default new VoiceManager();



