import ScoreIndexLayer from './ScoreIndexLayer';
import ScoreNextLayer from './ScoreNextLayer';
import { goBack } from '../common/invoke';
import { RES_SCORE } from '../resource/score';
import { TYPE_LIST, getAssess } from './const';

const FLOOR_COUNT = 3;
const INTERVAL = 0.2;
const ScoreScene = cc.Scene.extend({
    _results: null,

    ctor(result) {
        this._super();
        this._data = getAssess(result);
        this._vy = 0;// 惯性速度
        this._moving = false;// 处理连续点击
        const { width, height } = cc.director.getWinSize();
        this._floor = 0;
        this._floorHeight = [];
        for (let i = 0; i < FLOOR_COUNT; i += 1) {
            this._floorHeight.push(height * (FLOOR_COUNT - i - 1) * -1);
        }

        this.container = new cc.LayerColor(cc.color(249, 255, 255));
        this.container.setContentSize(cc.size(width, height * FLOOR_COUNT));
        this.container.setPositionY(this._floorHeight[this._floor]);
        this.container.addChild(new ScoreIndexLayer(-this._floorHeight[0], this._data));
        const menu = new cc.Menu();
        menu.setPosition(cc.p(cc.winSize.width / 2, 30 - this._floorHeight[0]));
        const menuItem = new cc.MenuItemImage(RES_SCORE.btn_next, null, this._onNext.bind(this));
        menuItem.setScale(0.75);
        menu.addChild(menuItem);
        this.container.addChild(menu);

        this.container.addChild(new ScoreNextLayer(-this._floorHeight[1], this._data, TYPE_LIST.slice(0, 2)));
        const menu1 = new cc.Menu();
        menu1.setPosition(cc.p(cc.winSize.width / 2, 30 - this._floorHeight[1]));
        const menuItem1 = new cc.MenuItemImage(RES_SCORE.btn_next, null, this._onNext.bind(this));
        menuItem1.setScale(0.75);
        menu1.addChild(menuItem1);
        this.container.addChild(menu1);

        this.container.addChild(new ScoreNextLayer(-this._floorHeight[2], this._data, TYPE_LIST.slice(2)));
        const menu2 = new cc.Menu();
        menu2.setPosition(cc.p(cc.winSize.width / 2, 50 - this._floorHeight[2]));
        const menuItem2 = new cc.MenuItemImage(RES_SCORE.btn_quit, null, this.onFinish.bind(this));
        menuItem2.setScale(0.7);
        menu2.addChild(menuItem2);
        this.container.addChild(menu2);

        this.addChild(this.container);
        const touchListener = cc.EventListener.create({
            event: cc.EventListener.TOUCH_ONE_BY_ONE,
            swallowTouches: true,
            onTouchBegan: () => true,
            onTouchMoved: (touch, event) => {
                if (this._moving) return 0;
                const target = event.getCurrentTarget();
                const { y } = touch.getDelta();
                const minHeight = this._floorHeight[0];
                if (y > 0 && target.y + y > 0) {
                    target.y = 0;
                    return true;
                }

                if (y < 0 && target.y + y < this._floorHeight[0]) {
                    target.y = minHeight;
                    return true;
                }
                this._vy = y;
                target.y += y;
                return true;
            },
            onTouchEnded: (touch, event) => {
                const target = event.getCurrentTarget();
                this._floor = this._floorHeight.length - 1 - Math.round((target.y + (this._vy * 3)) / -height);
                this._toFloor(this._floor);
            },
        });
        cc.eventManager.addListener(touchListener, this.container);
    },

    _toFloor(floor) {
        if (!this._moving) {
            if (floor < 0) this._floor = 0;
            if (floor >= FLOOR_COUNT) this._floor = FLOOR_COUNT - 1;
            const action = cc.moveTo(INTERVAL, 0, this._floorHeight[this._floor]);
            this._moving = true;
            this.container.runAction(action);
            this.container.scheduleOnce(() => {
                this._moving = false;
            }, INTERVAL);
        }
    },

    _onNext() {
        this._floor += 1;
        this._toFloor(this._floor);
    },

    onFinish() {
        goBack();
    },
});

export default ScoreScene;



