import { RES_APPLE } from '../../resource/apple';
import { generatePlatePosition, isSamePosition } from './position';
import { RoundTimeout, RedPointDisappearTimeout } from './round';
import TimeoutLayer from '../../common/TimeoutLayer';
import { EXAM_KEY_APPLE } from '../../constant/exam';

const TAG_RED_POINT = 2;
const TAG_APPLE = 3;
const TAG_DISTURBER = 4;
const TAG_SELECTED = 5;


const AppleLayer = cc.Layer.extend({
    _cbSelect: null,
    _begin: null,
    _react_begin: null,
    _roundOver: false,

    ctor({
        plateCount, redPointPosition, applePosition, disturberPosition, cbSelect,
    }) {
        this._super();

        this.applePosition = applePosition;
        this.redPointPosition = redPointPosition;
        this.disturberPosition = disturberPosition;
        this._cbSelect = cbSelect;
        this._roundOver = false;

        this.platePositions = generatePlatePosition(plateCount);

        const menu = new cc.Menu();
        menu.setPosition(0, 0);

        for (let i = 0; i < plateCount; i += 1) {
            const plateMenuItem = new cc.MenuItemImage(RES_APPLE.plate, null, this.onPlatePressed, this);
            plateMenuItem.setPosition(this.platePositions[i]);
            menu.addChild(plateMenuItem, 0, i);
        }

        this.addChild(menu);

        const redPoint = new cc.Sprite(RES_APPLE.red_point);
        redPoint.setPosition(this.redPointPosition);
        this.addChild(redPoint, 1, TAG_RED_POINT);
    },

    onEnter() {
        this._super();
        this._begin = new Date();
        this.scheduleOnce(this.redPointDisappear, RedPointDisappearTimeout);
    },

    redPointDisappear() {
        this.removeChildByTag(TAG_RED_POINT);
        // TODO: 播放语音提示
        this.scheduleOnce(this.appleAppear, 0.1);
    },

    appleAppear() {
        const apple = new cc.Sprite(RES_APPLE.apple);
        apple.setPosition(this.applePosition);
        this.addChild(apple, 1, TAG_APPLE);
        this._react_begin = new Date();

        if (this.disturberPosition) {
            this.disturberAppear();
        }

        this.scheduleOnce(this.onSelectTimeout, RoundTimeout);
    },

    disturberAppear() {
        const disturber = new cc.Sprite(RES_APPLE.strawberry);
        disturber.setPosition(this.disturberPosition);
        this.addChild(disturber, 1, TAG_DISTURBER);
    },

    onPlatePressed(menuItem) {
        if (this._roundOver) {
            return;
        }

        if (this._react_begin) {
            const position = menuItem.getPosition();
            this.selectedAppear(position);
            const result = {
                success: isSamePosition(position, this.applePosition),
                reaction: new Date().getTime() - this._react_begin.getTime(),
                timeout: false,
            };

            if (this._cbSelect) {
                this._roundOver = true;
                this._cbSelect(result);
            }
        }
    },

    selectedAppear(position) {
        const selector = new cc.Sprite(RES_APPLE.selected);
        selector.setPosition(position);
        this.addChild(selector, 1, TAG_SELECTED);
    },

    onSelectTimeout() {
        this._roundOver = true;

        if (this._cbSelect) {
            this._cbSelect({
                success: false,
                reaction: RoundTimeout * 1000,
                timeout: true,
            });
        }

        this.addChild(new TimeoutLayer(EXAM_KEY_APPLE));
    },
});

export default AppleLayer;



