import calcAttention from '../ablity/attention';
import calcMemory from '../ablity/memory';
import calcSolution from '../ablity/solution';
import calcExecutive from '../ablity/executive';
import {
    ABLITY_KEY_ATTENTION,
    ABLITY_KEY_MEMORY,
    ABLITY_KEY_SOLUTION,
    ABLITY_KEY_EXECUTIVE,
} from '../../../constant/exam';

export default function calcCognition(result) {
    const attentionResult = calcAttention(result);
    const memoryResult = calcMemory(result);
    const solutionResult = calcSolution(result);
    const executiveResult = calcExecutive(result);

    const abilitys = {
        [ABLITY_KEY_ATTENTION]: attentionResult.result,
        [ABLITY_KEY_MEMORY]: memoryResult.result,
        [ABLITY_KEY_SOLUTION]: solutionResult.result,
        [ABLITY_KEY_EXECUTIVE]: executiveResult.result,
    };

    const totalTime = attentionResult.totalTime + memoryResult.totalTime +
        solutionResult.totalTime + executiveResult.totalTime;

    return { abilitys, totalTime };
}



