import TimeoutLayer from '../../common/TimeoutLayer';
import NumberSprite from './NumberSprite';
import { RES_NUMBER } from '../../resource/number';
import { RES_COMMON } from '../../resource/common';
import { EXAM_KEY_NUMBER } from '../../constant/exam';
import VoiceManager from '../../common/voiceManager';
import HandleUtil from '../base/HandleUtil';
import ErrorTips from '../base/ErrorTips';

const POSITION_WAIT = [cc.p(506, 566), cc.p(788, 566)];
const POSITION_BTN_SELECT = cc.p(1118, 554);
const POSITION_ITEMS = [
    cc.p(206, 340),
    cc.p(434, 340),
    cc.p(664, 340),
    cc.p(892, 340),
    cc.p(1122, 340),
    cc.p(206, 132),
    cc.p(434, 132),
    cc.p(664, 132),
    cc.p(892, 132),
    cc.p(1122, 132),
];

const TAG_MENU = 1;
const TAG_BTN_CONFIRM = 100;
const TAGS_SELECTED = [200, 201];

const NoItem = -1;

const SelectLayer = cc.Layer.extend({
    _numbers: null,
    _cbDone: null,
    _selected: null,
    _begin: null,
    _userActions: null,
    _roundOver: null,
    _guide: null,
    _hand: null,
    _error: null,
    _onpress: null,
    _onGuideOverLayer: null,
    ctor(numbers, cbDone, isGuide, GuideOverLayer) {
        this._super();

        this._numbers = numbers;
        this._cbDone = cbDone;
        this._selected = [NoItem, NoItem];
        this._userActions = [];
        this._roundOver = false;
        this._guide = isGuide;
        this._onpress = false;
        this._onGuideOverLayer = GuideOverLayer;
        this.showWaitArea();
        this.showSelectItems();

        const menu = new cc.Menu();
        menu.setPosition(cc.p(0, 0));
        this._hand = new HandleUtil(menu, false);
        this._error = new ErrorTips(menu, false);
        this.addChild(menu);
    },

    onEnter() {
        this._super();

        this._begin = new Date();
        this.scheduleOnce(this.onRoundTimeout, 100000);
    },

    showWaitArea() {
        POSITION_WAIT.map((pos) => {
            const waitSprite = new cc.Sprite(RES_NUMBER.bg_wait);
            waitSprite.setPosition(pos);
            this.addChild(waitSprite);

            return null;
        });
        if (this._guide) {
            this.onVoice(7);
            this.scheduleOnce(this.onShowHand.bind(this, cc.p(1000, 270)), 11);
            this.scheduleOnce(this.onPress.bind(this, true), 11);
        }
    },

    showSelectItems() {
        const menu = new cc.Menu();
        menu.setPosition(cc.p(0, 0));
        const btnConfirm = new cc.MenuItemImage(
            RES_COMMON.btn_confirm_normal,
            RES_COMMON.btn_confirm_pressed,
            RES_COMMON.btn_confirm_disable,
            this.onConfirmSelected,
            this,
        );

        btnConfirm.setEnabled(false);
        btnConfirm.setPosition(POSITION_BTN_SELECT);
        menu.addChild(btnConfirm, 0, TAG_BTN_CONFIRM);

        POSITION_ITEMS.map((pos, index) => {
            const numberSprite = new NumberSprite(index, NumberSprite.Small);
            const menuItem = new cc.MenuItemSprite(numberSprite, null, this.onItemTouched, this);
            menuItem.setPosition(pos);
            menu.addChild(menuItem, 0, index);

            return null;
        });

        this.addChild(menu, 0, TAG_MENU);
    },

    onItemTouched(item, i) {
        this._error.hide();
        if (i === 0) {
            this.onPress(true);
        }
        let tag = '';
        if (item === 3 && this._guide) {
            tag = item;
        } else {
            tag = item.getTag();
        }
        const selectedIndex = this._selected.indexOf(tag);
        const noItemIndex = this._selected.indexOf(NoItem);
        if (!this._onpress && this._guide) {
            return;
        }
        if (this._guide && this._selected[0] !== -1 && this._selected[1] !== -1) {
            return;
        }
        if (this._guide && this._selected[0] === 3 && tag === 3) {
            return;
        }
        if (this._guide && this._selected[0] === 3 && tag !== 7) {
            VoiceManager.playVoice(RES_NUMBER.number_audio_15);
            return;
        }
        if (this._guide && tag !== 3 && tag !== 7) {
            this._error.show(cc.p(item.x + 55, item.y - 55));
            VoiceManager.playVoice(RES_NUMBER.number_audio_8, () => {
                this._error.hide();
            });
            return;
        }
        if (this._guide && tag === 7 && this._selected[0] === 7) {
            return;
        }
        if (this._guide && tag === 3 && i !== 0) {
            this._hand.hide();
            this.onPress(false);
            VoiceManager.playVoice(RES_NUMBER.number_audio_9);


            if (this._selected[0] === -1) {
                this.scheduleOnce(this.onShowHand.bind(this, cc.p(614, 494)), 6);
                this.scheduleOnce(this.onUnselect.bind(this, 3, 1), 8);

                this.scheduleOnce(this.onVoice.bind(this, 10), 10);
            }
            if (this._selected[0] === 7) {
                this.scheduleOnce(this.onShowHand.bind(this, cc.p(880, 494)), 6);

                this.scheduleOnce(this.onUnselect.bind(this, 3, 1), 8);

                this.scheduleOnce(this.onVoice.bind(this, 14), 12);

                this.scheduleOnce(this.onpress12, 14);
            }
        }
        if (this._guide && tag === 7 && this._selected[0] === -1) {
            VoiceManager.playVoice(RES_NUMBER.number_audio_11);
            this.scheduleOnce(this.onPress.bind(this, true), 2);
        }
        if (this._guide && tag === 7 && this._selected[0] === 3) {
            this._hand.hide();
            VoiceManager.playVoice(RES_NUMBER.number_audio_12);
            this.scheduleOnce(this.onpress12, 8);
        }
        this._userActions.push({
            time: new Date(),
            index: tag,
        });

        if (noItemIndex === -1 && selectedIndex === -1) {
            return;
        }

        if (selectedIndex === -1) {
            this._selected[noItemIndex] = tag;
        } else {
            this._selected[selectedIndex] = NoItem;
        }

        this.changeItemStatus(tag, selectedIndex === -1);
        this.showSelected();
        this.updateConfirmBtn();
        this.onPress(false);
    },
    onpress12() {
        this.onShowHand(cc.p(1250, 480));
        this.onPress(true);
    },
    onShowHand(position) {
        this._hand.show(position);
    },
    onPress(state) {
        if (state) {
            this._onpress = true;
        } else {
            this._onpress = false;
        }
    },
    onpress7() {
        this.onShowHand(cc.p(760, 79));
        this.onPress(true);
    },
    onVoice(n) {
        if (n === 10) {
            this.scheduleOnce(this.onpress7.bind(this), 6);
            VoiceManager.playVoice(RES_NUMBER.number_audio_10);
        }
        if (n === 7) {
            VoiceManager.playVoice(RES_NUMBER.number_audio_7);
        }
        if (n === 12) {
            VoiceManager.playVoice(RES_NUMBER.number_audio_12);
        }
        if (n === 14) {
            VoiceManager.playVoice(RES_NUMBER.number_audio_14);
        }
    },
    showSelected() {
        const menu = this.getChildByTag(TAG_MENU);

        this._selected.map((selected, index) => {
            const selectedSprite = menu.getChildByTag(TAGS_SELECTED[index]);

            if (selected === NoItem && selectedSprite) {
                menu.removeChild(selectedSprite);
            }

            if (selectedSprite && selectedSprite.getUserData() !== selected) {
                menu.removeChild(selectedSprite);
            }

            if (!selectedSprite && selected !== NoItem) {
                const numberSprite = new NumberSprite(selected, NumberSprite.Small);
                let menuItem;
                if (this._guide) {
                    menuItem = new cc.MenuItemSprite(numberSprite, null, null, this);
                } else {
                    menuItem = new cc.MenuItemSprite(numberSprite, null, this.onUnselect, this);
                }
                menuItem.setPosition(POSITION_WAIT[index]);
                menuItem.setUserData(selected);
                menu.addChild(menuItem, 0, TAGS_SELECTED[index]);
            }

            return null;
        });
    },

    changeItemStatus(number, selected) {
        const menu = this.getChildByTag(TAG_MENU);
        menu.removeChildByTag(number);
        const itemSprite = new NumberSprite(number, selected ? NumberSprite.SmallSelected : NumberSprite.Small);
        const menuItem = new cc.MenuItemSprite(itemSprite, null, this.onItemTouched, this);
        menuItem.setPosition(POSITION_ITEMS[number]);
        menu.addChild(menuItem, 0, number);
    },
    onUnselect(item, i) {
        if (i) {
            this.onPress(true);
        }
        if (!this._onpress && this._guide) {
            return;
        }
        let unselectNumber = '';
        if (item === 3) {
            unselectNumber = 3;
            this._hand.hide();
            this.scheduleOnce(this.onItemTouched.bind(this, 3, 0), 2);
        } else {
            unselectNumber = item.getUserData();
        }

        const unselectIndex = this._selected.indexOf(unselectNumber);

        this._userActions.push({
            time: new Date(),
            index: POSITION_ITEMS.length + unselectIndex,
        });

        if (unselectIndex !== -1) {
            this._selected[unselectIndex] = NoItem;
        }

        this.changeItemStatus(unselectNumber, false);
        this.showSelected();
        this.updateConfirmBtn();
        this.onPress(false);
    },

    updateConfirmBtn() {
        const menu = this.getChildByTag(TAG_MENU);
        const btnConfirm = menu.getChildByTag(TAG_BTN_CONFIRM);

        let hasSelected = false;

        for (let i = 0; i < this._selected.length; i += 1) {
            if (this._selected[i] !== NoItem) {
                hasSelected = true;
                break;
            }
        }

        btnConfirm.setEnabled(hasSelected);
    },

    onRoundTimeout() {
        this.addChild(new TimeoutLayer(EXAM_KEY_NUMBER));

        this.onConfirmSelected();
    },
    onConfirmSelected() {
        if (this._guide && this._onpress && this._selected[1] !== -1) {
            this._hand.hide();
            VoiceManager.playVoice(RES_NUMBER.number_audio_13);
            this._onGuideOverLayer();
            this.onPress(false);
            return;
        }
        if (this._roundOver) {
            return;
        }

        const result = {
            numbers: this._numbers,
            selected: this._selected,
            reaction: new Date().getTime() - this._begin.getTime(),
            userActions: this._userActions,
        };

        if (this._cbDone) {
            this._roundOver = true;
            this._cbDone(result);
        }
    },
});

export default SelectLayer;



