import { RES_NUMBER } from '../../resource/number';
import showIntroScene from '../base/IntroScene';
import showNumberGuideScene from './NumberGuideScene';
import BGLayer from './BGLayer';
import { EXAM_KEY_NUMBER } from '../../constant/exam';

export default function () {
    showIntroScene(
        {
            bgLayer: new BGLayer(),
            intro: RES_NUMBER.intro_img,
            btn: RES_NUMBER.btn_img,
            voice: RES_NUMBER.number_audio_1,
        },
        showNumberGuideScene,
        EXAM_KEY_NUMBER,
    );
}



