import { RES_SHAPE } from '../../resource/shape';
import showIntroScene from '../base/IntroScene';
import showGuideScene from './ShapeGuideScene';
import BGLayer from './BGLayer';
import { EXAM_KEY_SHAPE } from '../../constant/exam';

export default function () {
    showIntroScene(
        {
            bgLayer: new BGLayer(),
            intro: RES_SHAPE.intro_img,
            btn: RES_SHAPE.btn_img,
            voice: RES_SHAPE.intro,
        },
        showGuideScene,
        EXAM_KEY_SHAPE,
    );
}



