import { RES_CONNECTION } from '../../resource/connection';
import showIntroScene from '../base/IntroScene';
import showConnectionGuideScene from './ConnectionGuideScene';
import BGLayer from './BGLayer';
import { EXAM_KEY_CONNECTION } from '../../constant/exam';

export default function () {
    showIntroScene(
        {
            bgLayer: new BGLayer(),
            intro: RES_CONNECTION.intro_img,
            btn: RES_CONNECTION.btn_img,
            voice: RES_CONNECTION.connection_name,
        },
        showConnectionGuideScene,
        EXAM_KEY_CONNECTION,
    );
}



