import {
    EXAM_KEY_SHAPE,
    LEVEL1,
    LEVEL2,
    LEVEL3,
    INVALID_REACTION,
} from '../../../constant/exam';
import configManager from '../../../config';

function calcShapeLevelScore(round1, round2, level) {
    const levelConfig = configManager.getLevelConfig(EXAM_KEY_SHAPE, level.index);

    const result = { items: [round1, round2], level: level.value };

    if (!round1.success && !round2.success) {
        result.average_reaction = INVALID_REACTION;
        result.accuracy = 0;
        result.score = 0;
        result.original_score = 0;

        return result;
    }

    let correctCount = 0;
    let correctTotalReaction = 0;

    result.items.map((item) => {
        if (item.success) {
            correctCount += 1;
            correctTotalReaction += item.reaction;
        }

        return item;
    });

    const averageReaction = correctTotalReaction / correctCount;

    let score = (((levelConfig.reaction_threshold - averageReaction) / levelConfig.standard_deviation) * 10) + 50;
    if (!round1.success || !round2.success) {
        score -= 10;
    }

    result.original_score = score;
    result.score = score;
    if (score > levelConfig.max_score) {
        result.score = levelConfig.max_score;
    }
    if (score < 0) {
        result.score = 0;
    }
    result.accuracy = correctCount / result.items.length;
    result.average_reaction = averageReaction;

    return result;
}

export default function calcShape(result) {
    const { begin, end, rounds } = result[EXAM_KEY_SHAPE];

    const level1 = calcShapeLevelScore(rounds[0], rounds[1], LEVEL1);
    const level2 = calcShapeLevelScore(rounds[2], rounds[3], LEVEL2);
    const level3 = calcShapeLevelScore(rounds[4], rounds[5], LEVEL3);

    const score = (level1.score * 0.2) + (level2.score * 0.3) + (level3.score * 0.5);
    return {
        score,
        begin,
        end,
        levels: [level1, level2, level3],
    };
}



