
const RoundRectLayout = ccui.Layout.extend({

    ctor(width = 0, height = 0, radius = 0) {
        this._super();
        this.setContentSize(cc.size(width, height));
        this.bgLayer = new cc.LayerColor();

        const drawNode = new cc.DrawNode();
        const clipper = new cc.ClippingNode();
        const p1 = cc.p(radius, 0);
        const p2 = cc.p(width - radius, height);
        const p3 = cc.p(0, radius);
        const p4 = cc.p(width, height - radius);
        const r1 = cc.p(radius, radius);
        const r2 = cc.p(width - radius, radius);
        const r3 = cc.p(radius, height - radius);
        const r4 = cc.p(width - radius, height - radius);

        drawNode.drawRect(p1, p2, null);
        drawNode.drawRect(p3, p4, null);
        drawNode.drawCircle(r1, radius, cc.degreesToRadians(0), 50, false, 10, null);
        drawNode.drawCircle(r2, radius, cc.degreesToRadians(0), 50, false, 10, null);
        drawNode.drawCircle(r3, radius, cc.degreesToRadians(0), 50, false, 10, null);
        drawNode.drawCircle(r4, radius, cc.degreesToRadians(0), 50, false, 10, null);
        clipper.setStencil(drawNode);
        this.addChild(clipper);
        clipper.addChild(this.bgLayer);
    },
    setColor(color) {
        this.bgLayer.setColor(color);
    },
    setOpacity(opacity) {
        this.bgLayer.setOpacity(opacity);
    },
});

export default RoundRectLayout;




