import { EXAM_KEY_APPLE } from '../../constant/exam';
import AppleLayer from './AppleLayer';
import { RoundPlateCount, RoundDelay, AllDoneDelay, generatePosition } from './round';
import showCompleteScene from '../base/CompleteScene';
import showScene from '../../common/showScene';
import BGLayer from './BGLayer';

const TAG_APPLE_LAYER = 1;

const AppleExamLayer = cc.Layer.extend({
    _roundInfos: [],
    _samePosition: [],
    _round: 0,
    _config: null,
    _examBegin: null,
    _redPointIndex: 0,
    _applePositionIndex: 0,
    _disturberPositionIndex: 0,

    ctor() {
        this._super();

        this._roundInfos = [];
        this._samePosition = [];
        this._examBegin = new Date();
        this._config = generatePosition();

        const bgLayer = new BGLayer();
        this.addChild(bgLayer);

        this.nextRound();
    },

    nextRound() {
        if (this.getChildByTag(TAG_APPLE_LAYER)) {
            this.removeChildByTag(TAG_APPLE_LAYER);
        }

        this.scheduleOnce(this.showAppleLayer, Math.random());
    },

    showAppleLayer() {
        const plateCount = RoundPlateCount[this._round];

        const {
            redPointIndex,
            redPointPosition,
            applePositionIndex,
            applePosition,
            disturberPositionIndex,
            disturberPosition,
        } = this._config[this._round];

        this._redPointIndex = redPointIndex;
        this._applePositionIndex = applePositionIndex;
        this._disturberPositionIndex = disturberPositionIndex;

        const appleLayer = new AppleLayer({
            plateCount,
            redPointPosition,
            applePosition,
            disturberPosition,
            cbSelect: this.onRoundOver.bind(this),
        });
        this.addChild(appleLayer, 0, TAG_APPLE_LAYER);
    },

    onRoundOver(roundInfo) {
        this._roundInfos.push({
            ...roundInfo,
            redPointIndex: this._redPointIndex,
            applePositionIndex: this._applePositionIndex,
            disturberPositionIndex: this._disturberPositionIndex,
        });

        this._round += 1;

        if (this._round < RoundPlateCount.length) {
            this.scheduleOnce(this.nextRound, RoundDelay);
        } else {
            this.scheduleOnce(this.onFinish, AllDoneDelay);
        }
    },

    onFinish() {
        showCompleteScene(EXAM_KEY_APPLE, { rounds: this._roundInfos, begin: this._examBegin, end: new Date() });
    },
});

export default function showAppleExamScene() {
    showScene(new AppleExamLayer());
}



