import shuffle from 'shuffle-array';

const RoundCount = [2, 2, 4, 4, 4, 4];
const RoundTimeouts = [60, 60, 60, 60, 60, 60];
const RoundType = ['color', 'shape', 'color', 'shape', 'mix', 'mix'];
const RoundDelay = 0.5;
const PartInterval = 0.5;
const RoundTimeoutDelay = 1;
const PartDuration = 2;
const AllDoneDelay = 2;
const Colors = ['red', 'blue', 'yellow', 'green', 'gray', 'purple'];
const Shapes = ['car', 'plane', 'ship', 'house', 'ufo', 'bird', 'fish', 'bag', 'umbrella', 'book'];

const POSITION_FOR_ONE = cc.p(659, 390);

const POSITION_FOR_TWO = [
    cc.p(437, 390),
    cc.p(881, 390),
];

const POSITION_FOR_FOUR = [
    cc.p(437, 220),
    cc.p(881, 220),
    cc.p(437, 560),
    cc.p(881, 560),
];

const generateSelect = function (count, type, color, shape, otherColors, otherShapes) {
    let result = [];
    switch (type) {
        case 'color':
            result = otherColors.slice(0, count - 1).map(otherColor => ({ color: otherColor, shape }));
            break;
        case 'shape':
            result = otherShapes.slice(0, count - 1).map(otherShape => ({ color, shape: otherShape }));
            break;
        case 'mix':
            result = shuffle([{ color, shape: otherShapes.pop() },
                { color: otherColors.pop(), shape },
                { color: otherColors.pop(), shape: otherShapes.pop() }]);
            break;
        default: break;
    }
    return result;
};

const RoundConfig = RoundCount.map((count, index) => {
    const otherColors = [...shuffle(Colors)];
    const color = otherColors.pop();
    const otherShapes = [...shuffle(Shapes)];
    const shape = otherShapes.pop();

    const target = { color, shape };

    const select = generateSelect(count, RoundType[index], color, shape, otherColors, otherShapes);
    select.splice(Math.floor(Math.random() * count), 0, target);
    return {
        color,
        shape,
        target,
        roundTimeout: RoundTimeouts[index],
        position: count === 2 ? POSITION_FOR_TWO : POSITION_FOR_FOUR,
        select,
    };
});

const guideConfig = {
    color: 'blue',
    shape: 'plane',
    target: { color: 'blue', shape: 'plane' },
    position: POSITION_FOR_TWO,
    select: [{ color: 'blue', shape: 'plane' }, { color: 'red', shape: 'plane' }],
};

export {
    RoundConfig,
    RoundCount,
    RoundDelay,
    RoundTimeoutDelay,
    AllDoneDelay,
    POSITION_FOR_ONE,
    PartDuration,
    PartInterval,
    guideConfig,
};



