import { RES_COMMON } from '../../resource/common';
import VoiceManager from '../../common/voiceManager';
import { EXAM_MODAL_BG_COLORS_STRING_BY_KEY, EXAM_BG_COLORS_STRING_BY_KEY } from '../../constant/common';
import { setBgColor } from '../../common/invoke';

const ExitGame = cc.Layer.extend({

    ctor(cbExit, cbStart, examKey) {
        this._super();
        this._cbExit = cbExit;
        this._cbStart = cbStart;
        this._bgColor = EXAM_BG_COLORS_STRING_BY_KEY[examKey];
        const menu = new cc.Menu();
        const { width } = cc.director.getWinSize();
        menu.setPosition(0, 0);
        VoiceManager.stopVoice();

        const bgLayer = new cc.LayerColor(cc.color(77, 77, 73, 242));
        this.addChild(bgLayer, 2, 1);

        setBgColor(EXAM_MODAL_BG_COLORS_STRING_BY_KEY[examKey]);

        bgLayer.addChild(menu, 2, 1);

        const guideSussOver = new cc.Sprite(RES_COMMON.exit_game_description);
        guideSussOver.setPosition((width / 2), 460);
        bgLayer.addChild(guideSussOver, 3);


        const btnGuideAgain = new cc.MenuItemImage(
            RES_COMMON.btn_exit_game,
            null,
            this.onExitPressed.bind(this),
            this,
        );
        btnGuideAgain.setPosition((width / 2) - 200, 184);
        menu.addChild(btnGuideAgain);

        const btnStart = new cc.MenuItemImage(RES_COMMON.btn_go_on, null, this.onStartPressed.bind(this), this);
        btnStart.setPosition((width / 2) + 200, 184);
        menu.addChild(btnStart);
    },

    onEnter() {
        this._super();

        this._listener = cc.eventManager.addListener(
            {
                event: cc.EventListener.TOUCH_ONE_BY_ONE,
                swallowTouches: true,
                onTouchBegan: function () { return true; },
                onTouchMoved: function () { return true; },
                onTouchEnded: function () { return true; },
            },
            this,
        );
    },

    onExit() {
        cc.eventManager.removeListener(this._listener);
        setBgColor(this._bgColor);

        this._super();
    },

    onExitPressed() {
        if (this._cbExit) {
            this._cbExit();
        }

        const fromApp = JSON.parse(window.sessionStorage.getItem('fromApp'));
        if (fromApp && fromApp.isFromApp) {
            window.parent.postMessage({ type: 'go_back' }, '*');
        }
    },

    onStartPressed() {
        this._cbStart();
        if (this.getChildByTag(1)) {
            this.removeChildByTag(1);
        }
    },
});


export default ExitGame;



