import { RES_GUIDE_OVER_LAYER } from '../resource/guideOverLayer';
import ModalLayer from './ModalLayer';

const GuideOverLayer = cc.Layer.extend({

    ctor(cbAgain, cbStart, examKey) {
        this._super();

        const modal = new ModalLayer(
            cbAgain,
            cbStart,
            examKey,
            {
                bgImage: RES_GUIDE_OVER_LAYER.bg,
                leftBtnImage: RES_GUIDE_OVER_LAYER.btn_again,
                rightBtnImage: RES_GUIDE_OVER_LAYER.btn_start,
            },
        );
        this.addChild(modal);
    },
});


export default GuideOverLayer;



