import {
    EXAM_KEY_CONNECTION,
    LEVEL1,
    LEVEL2,
    INVALID_REACTION,
} from '../../../constant/exam';
import configManager from '../../../config';


function calcConnectionLevelScore(round, level) {
    const levelConfig = configManager.getLevelConfig(EXAM_KEY_CONNECTION, level.index);

    const result = { items: [round], level: level.value };

    if (round.correct === 0) {
        result.average_reaction = INVALID_REACTION;
        result.accuracy = 0;
        result.score = 0;
        result.original_score = 0;

        return result;
    }

    const accuracy = round.correct / round.count;
    const score = (((((levelConfig.reaction_threshold - round.reaction) / levelConfig.standard_deviation) * 10) + 50)
    * 0.5) + (accuracy * 25);

    result.original_score = score;
    result.score = score;
    if (score > levelConfig.max_score) {
        result.score = levelConfig.max_score;
    }
    if (score < 0) {
        result.score = 0;
    }
    result.average_reaction = round.reaction;
    result.accuracy = accuracy;

    return result;
}

export default function calcNumber(result) {
    const { begin, end, rounds } = result[EXAM_KEY_CONNECTION];

    const level1 = calcConnectionLevelScore(rounds[0], LEVEL1);
    const level2 = calcConnectionLevelScore(rounds[1], LEVEL2);

    const score = (level1.score * 0.4) + (level2.score * 0.6);

    return {
        score,
        begin,
        end,
        levels: [level1, level2],
    };
}



