import showScene from '../../common/showScene';
import showConnectionExamScene from './ConnectionExamScene';
import { RES_CONNECTION } from '../../resource/connection';
import { EXAM_KEY_CONNECTION } from '../../constant/exam';
import { guideConnection } from './round';
import VoiceManager from '../../common/voiceManager';
import GuideOverLayer from '../../common/GuideOverLayer';
import HeaderLayer from '../base/HeaderLayer';
import HandleUtil from '../base/HandleUtil';
import BGLayer from './BGLayer';
import GuideFirstPage from '../base/GuideFirstPage';

import PointSprite from './PointSprite';

const TAG_MENU = 1;
const ORDER_MENU = 1;
// const ORDER_TIMEOUTLAYER = 2;
const TAG_STAFF_BASE = 100;

const COLOR = [PointSprite.Blue, PointSprite.Green];

class ColorTypeSelector {
    constructor() {
        this.index = 1;
    }

    next() {
        const type = COLOR[this.index];

        this.index += 1;

        if (this.index === COLOR.length) {
            this.index = 0;
        }

        return type;
    }
}

const ConnectionGuideLayer = cc.Layer.extend({
    _config: null,
    _currentIndex: 0,
    _hand: null,

    ctor() {
        this._super();
        this._config = guideConnection;
        this._drawNode = new cc.DrawNode();
        this._currentIndex = 0;
        this._hand = null;
        const bgLayer = new BGLayer();
        this.addChild(bgLayer);
        this.addChild(new GuideFirstPage(EXAM_KEY_CONNECTION, 5, new BGLayer()), 88);
        this.addChild(this._drawNode);
        const header = new HeaderLayer(
            EXAM_KEY_CONNECTION,
            this.onSkip.bind(this),
            null,
            null,
            this.onGuideAgain.bind(this),
        );
        this.addChild(header, 99);

        this.showPoints();
        this.playGuideVoice(RES_CONNECTION.connection_into, this.renderOne.bind(this));
    },

    onEnter() {
        this._super();
    },

    renderOne() {
        // const menu = this.getChildByTag(TAG_MENU);
        const { correct } = this._config;
        this._hand.show(cc.p(correct[0].x + 80, correct[0].y - 110));
        this.playGuideVoice(RES_CONNECTION.connection_one, this.renderClick.bind(this, true));
    },

    renderClick(flag) {
        const menu = this.getChildByTag(TAG_MENU);
        const { correct } = this._config;
        correct.map((position, index) => {
            if (this._currentIndex <= index) {
                menu.getChildByTag(index).setEnabled(flag);
            }
            if (index !== correct.length - 1) {
                menu.getChildByTag(TAG_STAFF_BASE + index).setEnabled(flag);
            }
            return position;
        });
    },

    showPoints() {
        const { correct, staff } = this._config;

        const menu = new cc.Menu();
        this._hand = new HandleUtil(menu, false);
        menu.setPosition(0, 0);
        this.addChild(menu, ORDER_MENU, TAG_MENU);

        const colorSelector = new ColorTypeSelector();

        correct.map((position, index) => {
            const type = colorSelector.next();

            const correctSprite = new PointSprite(index, type);
            const correctItem = new cc.MenuItemSprite(correctSprite, null, this.onPointTouch, this);
            correctItem.setPosition(position);
            correctItem.setEnabled(false);
            menu.addChild(correctItem, 0, index);

            // if (index === 0) {
            //     correctItem.setEnabled(false);
            // }
            if (index !== correct.length - 1) {
                const staffSprite = new PointSprite(index + 1, type);
                const staffItem = new cc.MenuItemSprite(staffSprite, null, this.onPointTouch, this);
                staffItem.setPosition(staff[index]);
                staffItem.setEnabled(false);
                menu.addChild(staffItem, 0, TAG_STAFF_BASE + index);
            }

            return position;
        });
    },

    onPointTouch(item) {
        this.renderClick(false);
        const tag = item.getTag();
        const { correct } = this._config;

        if (tag >= TAG_STAFF_BASE || tag !== this._currentIndex) {
            item.getNormalImage().showError();
            this.playGuideVoice(RES_CONNECTION.connection_error, this.renderClick.bind(this, true));
        } else {
            item.setEnabled(false);
            item.getNormalImage().setSelected();
            if (this._currentIndex === 0) {
                const { x, y } = correct[this._currentIndex + 1];
                this._hand.move(0, cc.p(x + 80, y - 110));
                this.playGuideVoice(RES_CONNECTION.connection_two, this.renderClick.bind(this, true));
            }
            if (this._currentIndex === 1) {
                const { x, y } = correct[this._currentIndex + 1];
                this._hand.move(0, cc.p(x + 80, y - 110));
                this.playGuideVoice(RES_CONNECTION.connection_three, this.renderClick.bind(this, true));
            }
            if (this._currentIndex === 2) {
                this._hand.hide();
                this.guideOver();
            }


            if (this._currentIndex !== 0) {
                this.drawLines();
            }

            this._currentIndex += 1;
        }
    },

    drawLines() {
        this._drawNode.drawSegment(
            this._config.correct[this._currentIndex],
            this._config.correct[this._currentIndex - 1],
            3,
            cc.color(8, 172, 162),
        );
    },


    playGuideVoice(path, cb) {
        VoiceManager.playVoice(path, cb);
    },
    onGuideAgain() {
        showConnectionGuideScene();
    },

    guideOver() {
        const overLayer = new GuideOverLayer(
            this.onGuideAgain.bind(this),
            this.onSkip.bind(this),
            EXAM_KEY_CONNECTION,
        );
        this.playGuideVoice(RES_CONNECTION.connection_end, () => {});
        this.addChild(overLayer, 999);
    },
    onSkip() {
        VoiceManager.stopVoice();
        showConnectionExamScene();
    },
});

export default function showConnectionGuideScene() {
    showScene(new ConnectionGuideLayer());
}



