import calcCandy from '../exam/candy';
import calcApple from '../exam/apple';
import { EXAM_KEY_CANDY, EXAM_KEY_APPLE } from '../../../constant/exam';
import updateResult from './updateResult';

export default function calcAttention(result) {
    const attentionResult = {
        totalTime: 0,
        result: { score: 0 },
    };

    updateResult(attentionResult, result, calcCandy, EXAM_KEY_CANDY);
    updateResult(attentionResult, result, calcApple, EXAM_KEY_APPLE);

    return attentionResult;
}



